import axios from 'axios';
export async function http(requestype, url, body = {}, optionns = false) {
  let baseurl = 'http://localhost:4000/api/';
  if (requestype === 'get') {
    try {
      let token = optionns
        ? {
            headers: {
              Authorization: 'Bearer ' + localStorage.getItem('token'),
            },
          }
        : {};
      console.log(token);
      let respose = await axios.get(baseurl + url, token);
      return respose;
    } catch (error) {
      throw new Error(error.message);
    }
  }
  if (requestype === 'post') {
    try {
      let token = optionns
        ? {
            headers: {
              Authorization: 'Bearer ' + localStorage.getItem('token'),
            },
          }
        : {};
      console.log(token);
      let respose = await axios.post(baseurl + url, body, token);
      return respose;
    } catch (error) {
      throw new Error(error.message);
    }
  }
}
