import React, { useState } from 'react';
import { FiX } from 'react-icons/fi';
import Date from '../../../../components/Date';
import ModalInput from '../../../../components/ModalInput';
import { http } from '../../../../http';
import { useStateValue } from '../../../../store/stateProvider';
import HackathonModal from './HackathonModal';

const CodingContestModal = () => {
  const [{ modal, fetch }, dispatch] = useStateValue();
  const [coding, setCoding] = useState({
    title: '',
    position: '',
  });

  const handleClick = () => {
    dispatch({
      type: 'SET_MODAL',
      modal: false,
    });
  };

  const handleSubmit = (value) => {
    dispatch({
      type: 'SET_MODAL',
      modal: false,
    });
    addcodingcontests(value);
  };

  const handleCodingChange = (event) => {
    const { value, name } = event.target;

    setCoding((prev) => {
      if (name === 'name') {
        return {
          ...prev,
          name: value,
        };
      } else if (name === 'position') {
        return {
          ...prev,
          position: value,
        };
      }
    });
  };
  async function addcodingcontests(placement) {
    try {
      let response = await http(
        'post',
        'codingcontests/add',
        { ...placement, verified: 'pending' },
        true
      );
    } catch (err) {
      console.log(err);
    }
  }
  return (
    <div className=' w-1/3 bg-white relative p-8'>
      <FiX
        onClick={() => handleClick()}
        className='font-semibold absolute top-4 text-xl cursor-pointer right-4'
      />
      <h1 className='font-bold text-lg'>Coding Contest Details</h1>
      <p className='text-dark text-sm'>Please add all information carefully</p>
      <div className='flex gap-3'>
        <ModalInput
          onChange={handleCodingChange}
          value={coding.name}
          title='Title'
          name='name'
          type='text'
        />
        <ModalInput
          onChange={handleCodingChange}
          value={coding.position}
          title='Position'
          name='position'
          type='text'
        />
      </div>
      <div className='text-center'>
        <button
          onClick={() => {
            handleSubmit(coding);
            console.log(coding);
            dispatch({
              type: 'SET_FETCH',
              modal: !fetch,
            });
          }}
          className='bg-primary text-sm font-semibold focus:outline-none delay-75 ease-linear duration-100 transform hover:-translate-y-1 outline-none text-white rounded-lg px-10 py-2 mt-8'
        >
          Submit
        </button>
      </div>
    </div>
  );
};

export default CodingContestModal;
