import React, { useState } from 'react';
import { FiX } from 'react-icons/fi';
import Date from '../../../../components/Date';
import ModalInput from '../../../../components/ModalInput';
import { http } from '../../../../http';
import { useStateValue } from '../../../../store/stateProvider';
import CodingContestModal from './CodingContestModal';
import HackathonModal from './HackathonModal';
import ProjectsModal from './ProjectsModal';

const PlacementModal = () => {
  const [newPlacements, setNewPlacements] = useState({
    company: '',
    position: '',
    startdate: null,
    status: '',
  });

  const [{ modal }, dispatch] = useStateValue();

  const handleClick = () => {
    dispatch({
      type: 'SET_MODAL',
      modal: false,
    });
  };

  const handleSubmit = (value) => {
    dispatch({
      type: 'SET_MODAL',
      modal: false,
    });
    addPlacement(value);
  };

  const handlePlacementChange = (event) => {
    const { value, name } = event.target;

    setNewPlacements((prev) => {
      if (name === 'company') {
        return {
          ...prev,
          company: value,
        };
      } else if (name === 'position') {
        return {
          ...prev,
          position: value,
        };
      } else if (name === 'status') {
        return {
          ...prev,
          status: value,
        };
      } else if (name === 'startdate') {
        return {
          ...prev,
          startdate: value,
        };
      }
    });
  };

  async function addPlacement(placement) {
    try {
      let response = await http(
        'post',
        'placements/add',
        { ...placement, verified: 'pending' },
        true
      );
    } catch (err) {
      console.log(err);
    }
  }

  return (
    <div className=' w-1/3 bg-white relative p-8'>
      <FiX
        onClick={() => handleClick()}
        className='font-semibold absolute top-4 text-xl cursor-pointer right-4'
      />

      <h1 className='font-bold text-lg'>Placement Details</h1>
      <p className='text-dark text-sm'>Please add all information carefully</p>
      <form className=''>
        <div className='flex gap-3'>
          <ModalInput
            title='Organisation'
            value={newPlacements.company}
            type='text'
            name='company'
            onChange={handlePlacementChange}
          />

          <ModalInput
            title='Applied for'
            value={newPlacements.position}
            type='text'
            name='position'
            onChange={handlePlacementChange}
          />
        </div>
        <div className='flex gap-3'>
          <Date
            title='Starting Date'
            value={newPlacements.startdate}
            name='startdate'
            onChange={handlePlacementChange}
          />
          <ModalInput
            title='Status'
            type='text'
            name='status'
            value={newPlacements.status}
            onChange={handlePlacementChange}
          />
        </div>
        <div className='text-center'>
          <button
            type='submit'
            onClick={() => {
              handleSubmit(newPlacements);
            }}
            className='bg-primary text-sm font-semibold focus:outline-none delay-75 ease-linear duration-100 transform hover:-translate-y-1 outline-none text-white rounded-lg px-10 py-2 mt-8'
          >
            Submit
          </button>
        </div>
      </form>
    </div>
  );
};

export default PlacementModal;
