import React from 'react';
import Header from '../../../components/Header';
import Overview from './Overview';
import { BrowserRouter as Router, Switch, Route, Link } from 'react-router-dom';
import Education from './Education';
import Achivements from './Achivements';
import Certifications from './Certifications';
import Placements from './Placements';
import {withRouter} from 'react-router-dom';

const Main = () => {
  return (
    <div className='bg-light w-full pl-16 pr-6'>
      {/* <Overview /> */}
      
        <Switch>
          <Route exact  path='/dashboard/overview'>
            <Overview />
          </Route>
       
        <Route   path='/dashboard/education' component={withRouter(Education)}/>
       
       
        <Route exact path='/dashboard/achivements'>
          <Achivements />
        </Route>
        <Route exact path='/dashboard/certifications'>
          <Certifications />
        </Route>
        <Route exact path='/dashboard/placements'>
          <Placements />
        </Route>
        </Switch>
      
     
    </div>
  );
};

export default withRouter(Main);
