import React, { useState } from 'react';
import { FiX } from 'react-icons/fi';
import Date from '../../../../components/Date';
import ModalInput from '../../../../components/ModalInput';
import { http } from '../../../../http';
import { useStateValue } from '../../../../store/stateProvider';
import CertificationsModal from './CertificationsModal';
import CodingContestModal from './CodingContestModal';
import HackathonModal from './HackathonModal';
import PlacementModal from './PlacementModal';
import ProjectsModal from './ProjectsModal';
import SkillModal from './SkillModal';

const InternshipModal = () => {
  const [internship, setInternship] = useState({
    organisation: '',
    role: '',
    start: '',
    end: '',
    duration: '',
    stipened: '',
  });

  const [{ modal }, dispatch] = useStateValue();

  const handleClick = () => {
    dispatch({
      type: 'SET_MODAL',
      modal: false,
    });
  };

  const handleSubmit = (value) => {
    dispatch({
      type: 'SET_MODAL',
      modal: false,
    });
    addinternships(value);
  };

  async function addinternships(placement) {
    try {
      let response = await http(
        'post',
        'internships/add',
        { ...placement },
        true
      );
    } catch (err) {
      console.log(err);
    }
  }

  const handleInternshipChange = (event) => {
    const { value, name } = event.target;

    setInternship((prev) => {
      if (name === 'organisation') {
        return {
          ...prev,
          organisation: value,
        };
      } else if (name === 'start') {
        return {
          ...prev,
          start: value,
        };
      } else if (name === 'end') {
        return {
          ...prev,
          end: value,
        };
      } else if (name === 'duration') {
        return {
          ...prev,
          duration: value,
        };
      } else if (name === 'stipened') {
        return {
          ...prev,
          stipened: value,
        };
      } else if (name === 'role') {
        return {
          ...prev,
          role: value,
        };
      }
    });
  };

  return (
    <div className=' w-1/3 bg-white relative p-8'>
      <FiX
        onClick={() => handleClick()}
        className='font-semibold absolute top-4 text-xl cursor-pointer right-4'
      />
      <h1 className='font-bold text-lg'>Internship Details</h1>
      <p className='text-dark text-sm'>Please add all information carefully</p>
      <div className='flex gap-3'>
        <ModalInput
          onChange={handleInternshipChange}
          value={internship.organisation}
          title='Organisation'
          name='organisation'
          type='text'
        />
        <ModalInput
          onChange={handleInternshipChange}
          title='Role'
          value={internship.role}
          name='role'
          type='text'
        />
      </div>
      <div className='flex gap-3'>
        <Date
          onChange={handleInternshipChange}
          name='start'
          value={internship.start}
          title='Start Date'
        />
        <Date
          onChange={handleInternshipChange}
          name='end'
          title='End Date'
          value={internship.end}
        />
      </div>
      <div className='flex gap-3'>
        <ModalInput
          value={internship.duration}
          name='duration'
          onChange={handleInternshipChange}
          title='Duration'
          type='text'
        />
        <ModalInput
          name='stipened'
          onChange={handleInternshipChange}
          title='Stipened'
          type='text'
          value={internship.stipened}
        />
      </div>
      <div className='text-center'>
        <button
          onClick={() => {
            handleSubmit(internship);
          }}
          className='bg-primary text-sm font-semibold focus:outline-none delay-75 ease-linear duration-100 transform hover:-translate-y-1 outline-none text-white rounded-lg px-10 py-2 mt-8'
        >
          Submit
        </button>
      </div>
    </div>
  );
};

export default InternshipModal;
