import React from 'react';
import logo from '../assets/logo.png';
import {
  FaChartPie,
  FaClipboardList,
  FaBook,
  FaTrophy,
  FaBuilding,
} from 'react-icons/fa';
import { AiFillSafetyCertificate, AiOutlineLogout } from 'react-icons/ai';
import { NavLink, useHistory } from 'react-router-dom';

const FacultySidebar = () => {
 const  history = useHistory()
  function logout(){
    localStorage.removeItem('token');
    localStorage.removeItem('facultytoken');
    localStorage.removeItem('user');
    localStorage.removeItem('facultyId');
    history.push('/faculty');

  }
  return (
    <div className='h-screen w-60'>
      <div className='h-screen w-60 bg-darkBlack pt-6 fixed'>
        <div className='logo flex items-center justify-center -ml-6 cursor-pointer'>
          <img draggable={false} className='h-8' src={logo} alt='logo' />
          <p className='text-xl ml-2 text-white font-semibold'>Métier</p>
        </div>

        {/* Navitem */}

        <NavLink
          to='/faculty/dashboard/overview'
          activeClassName='active'
          className='nav-item flex items-center gap-4 hover:text-white cursor-pointe  py-3 text-dark pl-8 mt-12'
        >
          <FaChartPie />
          <span className=''>Overview</span>
        </NavLink>

        {/* Navitem end */}
        {/* Navitem */}
        <NavLink
          to='/faculty/dashboard/classlist'
          activeClassName='active'
          className='nav-item flex items-center gap-4 hover:text-white cursor-pointe  py-3 text-dark pl-8 mt-5'
        >
          <FaClipboardList />
          <span className=''>Class List</span>
        </NavLink>
        {/* Navitem end */}
        {/* Navitem */}
        <NavLink
          to='/faculty/dashboard/verifications'
          activeClassName='active'
          className='nav-item flex items-center gap-4 hover:text-white cursor-pointe  py-3 text-dark pl-8 mt-5'
        >
          <AiFillSafetyCertificate />
          <span className=''>Verification</span>
        </NavLink>

        {/* Navitem */}
        <div className='nav-item flex items-center absolute bottom-8 gap-4 hover:text-white cursor-pointer py-3 text-navtext pl-8 mt-3'>
          <AiOutlineLogout />
          <span onClick={()=>logout()}>Logout</span>
        </div>
        {/* Navitem end */}
      </div>
    </div>
  );
};

export default FacultySidebar;
