import React, { useEffect, useState } from 'react';
import Card from '../../../components/Card';
import Header from '../../../components/Header';
import Line from './Line';
import { useStateValue } from '../../../store/stateProvider';
import { http } from './../../../http';

const Overview = () => {
  const [edit, setEdit] = useState(false);
  const [userdata, setuserdata] = useState({});
  const [{ user }, dispatch] = useStateValue();
 // const [chng, setchng] = useState(8);
  const handleUserDetails = () => {
    dispatch({
      type: 'SET_USER',
      user: userdata,
    });
  };
  async function getuserdetails() {
    //let token = localStorage.getItem('token');
    return await http('get', 'userdetail/get', {}, true);
  }
  // useEffect(async ()=>{
  //   let ac = new  AbortController();
  //   try{
  //     let response = await getuserdetails();
  //     console.log(response.data);
  //     setuserdata(response.data)
  //     handleUserDetails();
  //   }catch(err){
  //     console.log(err);
  //   }
  //   ac.abort();

  // },[chng])
  const [college, setCollege] = useState([]);
  const [personal, setPersonal] = useState(false);
  const handleFetch = async () => {
    let ac = new AbortController();
    try {
      let response = await getuserdetails();
      let response1 = await http('get', '/education/get', {}, true);
      console.log(response.data);
      console.log(response1.data);
      setCollege(response1.data.college);
      setuserdata(response.data);
      handleUserDetails();
    } catch (err) {
      console.log(err);
    }
    ac.abort();
  };
  async function userdetailupdate(user) {
    try {
      let response = await http('post', 'personaldetail/update', user, true);
      setPersonal(true);
      console.log(response);
    } catch (er) {
      console.log(er.message);
    }
  }
  useEffect(() => {
    handleFetch();
  }, [personal]);

  const [newEntry, setNewEntry] = useState({
    email: userdata.email,
    personalemail: userdata.personalemail,
    number: userdata.number,
    parentContact: userdata.parentContact,
  });

  const handleDataChange = (event) => {
    const { value, name } = event.target;

    setNewEntry((prev) => {
      if (name === 'email') {
        return {
          ...prev,
          email: value,
        };
      } else if (name === 'personalemail') {
        return {
          ...prev,
          personalemail: value,
        };
      } else if (name === 'number') {
        return {
          ...prev,
          number: value,
        };
      } else if (name === 'parentContact') {
        return {
          ...prev,
          parentContact: value,
        };
      }
    });
  };

  return (
    <div>
      <Header
        headline='Overview'
        type='student'
        name={userdata.name}
        photo={userdata.photograph}
      />
      {/* cards section */}
      <div className='flex gap-8 px-5 mt-10'>
        <Card
          width='full'
          title='Placements'
          value={!!userdata.placements ? userdata.placements : 0}
        />
        <Card width='full' title='CGPA' value={userdata.CGPA} />
        <Card
          width='full'
          title='Certifications'
          value={!!userdata.certifications ? userdata.certifications : 0}
        />
        <Card
          width='full'
          title='Achivements'
          value={!!userdata.achivements ? userdata.achivements : 0}
        />
      </div>
      {/* academic performance */}
      <div className='border-2 border-border bg-white h- rounded-lg mx-5 mt-8 flex'>
        <div className='w-2/3 py-5 px-6 border-r-2 border-border'>
          <h1 className='font-bold text-lg'>Acadamic Performance</h1>
          <p className='text-dark text-sm'>Semester-wise cgpa distribution</p>
          <div className='h-52'>
            <Line cgpa={college} />
          </div>
        </div>
        <div className='w-1/3'>
          <div className='mt-5 pb-5 border-b-2 border-border'>
            <h1 className='text-dark font-semibold text-center'>Internships</h1>
            <p className='text-center font-bold text-2xl'>
              {!!userdata.internships ? userdata.internships : 0}
            </p>
          </div>

          <div className='mt-5 pb-5 border-b-2 border-border'>
            <h1 className='text-dark font-semibold text-center'>
              Total Arears
            </h1>
            <p className='text-center font-bold text-2xl'>0</p>
          </div>
          <div className='mt-5 pb-5'>
            <h1 className='text-dark font-semibold text-center'>
              Standing Arears
            </h1>
            <p className='text-center font-bold text-2xl'>0</p>
          </div>
        </div>
      </div>
      {/* other details */}
      <div className='flex mx-5 gap-10 my-10'>
        {/* class details */}
        <div className='w-full border-2 bg-white border-border rounded-lg'>
          <div className='py-5 px-6 flex justify-between'>
            <div>
              <h1 className='font-bold text-lg'>Class Details</h1>
              <p className='text-dark text-sm'>Your home to success</p>
            </div>
            <h1 className='text-primary cursor-pointer font-semibold'>
              Contact
            </h1>
          </div>
          <div className='w-full flex justify-between px-8 py-4 border-b border-border'>
            <p className='text-sm'>Department</p>
            <p className='text-sm'>{userdata.dept}</p>
          </div>
          <div className='w-full flex justify-between px-8 py-4 border-b border-border'>
            <p className='text-sm'>Specialization</p>
            <p className='text-sm'>{userdata.specilization}</p>
          </div>
          <div className='w-full flex justify-between px-8 py-4 border-b border-border'>
            <p className='text-sm'>Section</p>
            <p className='text-sm'>{userdata.section}</p>
          </div>
          <div className='w-full flex justify-between px-8 py-4'>
            <p className='text-sm'>Faculty Advisor</p>
            <p className='text-sm'>{userdata.faculty}</p>
          </div>
        </div>

        {/* personal details */}
        <div className='w-full border-2 bg-white border-border rounded-lg'>
          <div className='py-5 px-6 flex justify-between'>
            <div>
              <h1 className='font-bold text-lg'>Personal Details</h1>
              <p className='text-dark text-sm'>Required for communication</p>
            </div>
            {edit ? (
              <h1
                className='text-primary cursor-pointer font-semibold'
                onClick={() => {
                  setEdit(!edit);
                  userdetailupdate(newEntry);
                }}
              >
                Save Details
              </h1>
            ) : (
              <h1
                className='text-primary cursor-pointer font-semibold'
                onClick={() => setEdit(!edit)}
              >
                Edit Details
              </h1>
            )}
          </div>
          <div className='w-full flex justify-between px-8 py-4 border-b border-border'>
            <p className='text-sm'>College Email</p>
            {edit ? (
              <input
                className='text-center text-xs bg-transparent focus:outline-none'
                placeholder='Enter Email'
                name='email'
                onChange={handleDataChange}
                value={newEntry.email}
              />
            ) : (
              <span className='text-sm'>{userdata.email}</span>
            )}
          </div>
          <div className='w-full flex justify-between px-8 py-4 border-b border-border'>
            <p className='text-sm'>Personal Email</p>
            {edit ? (
              <input
                value={newEntry.personalemail}
                className='text-center text-xs bg-transparent focus:outline-none'
                placeholder='Enter Email'
                name='personalemail'
                onChange={handleDataChange}
              />
            ) : (
              <span className='text-sm'>
                {!!userdata.personalemail ? userdata.personalemail : ''}
              </span>
            )}
          </div>
          <div className='w-full flex justify-between px-8 py-4 border-b border-border'>
            <p className='text-sm'>Contact Number</p>
            {edit ? (
              <input
                className='text-center text-xs bg-transparent focus:outline-none'
                placeholder='Enter Contact'
                name='number'
                value={newEntry.number}
                onChange={handleDataChange}
              />
            ) : (
              <span className='text-sm'>{userdata.number}</span>
            )}
          </div>
          <div className='w-full flex justify-between px-8 py-4'>
            <p className='text-sm'>Parents Contact Number</p>
            {edit ? (
              <input
                className='text-center text-xs bg-transparent focus:outline-none'
                placeholder='Enter Contact'
                name='parentContact'
                value={newEntry.parentContact}
                onChange={handleDataChange}
              />
            ) : (
              <span className='text-sm'>{userdata.parentContact}</span>
            )}
          </div>
        </div>
      </div>
    </div>
  );
};

export default Overview;
