import React, { useState } from 'react';
import logo from '../assets/logo.png';
import Button from '../components/Button';
import Input from '../components/Input';
import {  useFormik } from 'formik';
//import Overview from './Dashboard/Student/Overview';
import { useHistory } from "react-router-dom";
import { useStateValue} from "./../store/stateProvider";

//import ReactDOM from "react-dom";
// import {
//   BrowserRouter as Router,
//   Switch,
//   useLocation,
//   Redirect,
//   Link,
//   Route
// } from "react-router-dom";
import { http } from '../http';

const FacultyLogin = () => {
  const history = useHistory();
  const [{ login }, dispatch] = useStateValue();
  function handleClick() {
    history.push('/faculty/dashboard/overview');
  }
    const handleloginDetails = () => {
    dispatch({
      type: 'SET_LOGIN',
      login: true,
    });
  };
  
  let loginurl = 'faculty/login';
  const [submit, setSubmit] = useState(false)
  //const [loginform, setloginform] = useState('');
  async function login1(value) {
    return await http('post', loginurl, value);
  }

  const validate = (values) => {
    const errors = {};

    if (!values.facultyId) {
      errors.facultyId = 'Required';
    } else if (!/[0-9]/i.test(values.facultyId)) {
      errors.facultyId = 'Invalid Registration Number';
    }

    if (!values.password) {
      errors.password = 'Required';
    } else if (values.password.length < 5) {
      errors.password = 'Must be 5 characters or more';
    } else if (values.password === '12345678') {
      errors.password = 'Must not be 12345678 !!!';
    }

    return errors;
  };

  const formik = useFormik({
    initialValues: {
      facultyId: '',
      password: '',
    },
    validate,
    onSubmit: async (values) => {
      console.log('submit');
      try {
        let response = await login1(values);
        console.log(response.data);
        localStorage.setItem('token', response.data.token);
        localStorage.setItem('user', response.data.user);
        setSubmit(true);
        localStorage.setItem('facultyId',response.data.id);
        handleClick();
        handleloginDetails();
        history.push('/faculty/dashboard/overview');
        
      console.log(JSON.stringify(values, null));
      }catch(err){

      }
  }});

  return (
    <div>
      <div className='flex w-screen h-screen'>
        <div className='h-screen bg-side bg-cover bg-black w-4/12 flex items-center'>
          <div className='w-full text-center'>
            <img className='mx-auto' src={logo} alt='logo' />
            <h1 className='text-3xl mt-3 text-center text-white font-semibold'>
              Métier
            </h1>

            <button
              className='outline-none button bg-white px-6 mt-36 text-lg py-3 rounded-lg'
              onClick={() => {
                history.push('/');
              }}
            >
              Login as Student
            </button>
            
          </div>
        </div>
        <div className='h-screen w-8/12 bg-light flex items-center'>
          <div className='w-screen mx-64'>
            <h1 className='text-2xl font-semibold'>Faculty Login</h1>
            <p className='text-dark mt-1'>We are glad to see you here</p>

            <form onSubmit={formik.handleSubmit}>
              <div className='relative'>
                <Input
                  name='facultyId'
                  title='Faculty Id'
                  type='text'
                  onBlur={formik.handleBlur}
                  onChange={formik.handleChange}
                  value={formik.values.facultyId}
                />
                {formik.touched.facultyId &&
                  formik.errors.facultyId && (
                    <p className='text-xs absolute top-full mt-1 font-semibold text-red-500'>
                      {formik.errors.facultyId}
                    </p>
                  )}
              </div>
              <Input
                name='password'
                title='Password'
                type='password'
                onBlur={formik.handleBlur}
                onChange={formik.handleChange}
                value={formik.values.password}
              />
              {formik.touched.password && formik.errors.password && (
                <p className='text-xs text-red-500'>{formik.errors.password}</p>
              )}
             
              <Button type='submit' name='Login' ></Button>
             
            </form>
         
          </div>
        </div>
      </div>
    </div>
  );
};

export default FacultyLogin;
