import React from 'react';

const colors = [
  'gray',
  'yellow',
  'green',
  'blue',
  'purple',
  'pink',
  'red',
  'indigo',
];

const Avatar = (props) => {
  const index = Math.floor(Math.random() * 7);
  //   setIndex(Math.floor(Math.random() * 7));
  return (
    <div>
      <div
        className={`h-9 w-9 rounded-full bg-${colors[index]}-400 flex items-center justify-center overflow-hidden`}
      >
        <span className='text-sm font-bold text-white'>{props.name}</span>
      </div>
    </div>
  );
};

export default Avatar;
