import React ,{useEffect, useState} from 'react';
import logo from '../assets/logo.png';
import { FaChartPie, FaBook, FaTrophy, FaBuilding } from 'react-icons/fa';
import { AiFillSafetyCertificate, AiOutlineLogout } from 'react-icons/ai';
import { NavLink, useHistory } from 'react-router-dom';
import {useStateValue} from '../store/stateProvider';
const Sidebar = () => {
  const history = useHistory();
    const [{login  }, dispatch] = useStateValue();
      const handleloginDetails = (param) => {
    dispatch({
      type: 'SET_LOGIN',
      login: param,
    });
  };
  function logout(){
    localStorage.removeItem('token');
    localStorage.removeItem('user');
    handleloginDetails(false);
    if(!!localStorage.getItem('facultytoken')){
      let facultytoken = localStorage.getItem('facultytoken')
      localStorage.setItem('token',facultytoken);
      localStorage.setItem('user','faculty')
    history.push('/faculty/dashboard/overview');
    } else {
      history.push('/');
    }
    
  }
  const [chngdetect, setChngdetect] = useState(false)
useEffect(() => {
  handleloginDetails(true)
}, [chngdetect])
  return (
    <div className='h-screen w-60'>
      <div className='h-screen w-60 bg-darkBlack pt-6 fixed'>
        <div className='logo flex items-center justify-center -ml-6 cursor-pointer'>
          <img draggable={false} className='h-8' src={logo} alt='logo' />
          <p className='text-xl ml-2 text-white font-semibold'>Métier</p>
        </div>

        {/* Navitem */}

        <NavLink
          to='/dashboard/overview'
          activeClassName='active'
          className='nav-item flex items-center gap-4 hover:text-white cursor-pointe  py-3 text-dark pl-8 mt-12'
        >
          <FaChartPie />
          <span className=''>Overview</span>
        </NavLink>

        {/* Navitem end */}
        {/* Navitem */}
        <NavLink
          to='/dashboard/education'
          activeClassName='active'
          className='nav-item flex items-center gap-4 hover:text-white cursor-pointe  py-3 text-dark pl-8 mt-5'
        >
          <FaBook />
          <span className=''>Education</span>
        </NavLink>
        {/* Navitem end */}
        {/* Navitem */}
        <NavLink
          to='/dashboard/achivements'
          activeClassName='active'
          className='nav-item flex items-center gap-4 hover:text-white cursor-pointe  py-3 text-dark pl-8 mt-5'
        >
          <FaTrophy />
          <span className=''>Achivements</span>
        </NavLink>
        {/* Navitem end */}
        {/* Navitem */}
        <NavLink
          to='/dashboard/certifications'
          activeClassName='active'
          className='nav-item flex items-center gap-4 hover:text-white cursor-pointe  py-3 text-dark pl-8 mt-5'
        >
          <AiFillSafetyCertificate />
          <span className=''>Certifications</span>
        </NavLink>
        {/* Navitem end */}
        {/* Navitem */}
        <NavLink
          to='/dashboard/placements'
          activeClassName='active'
          className='nav-item flex items-center gap-4 hover:text-white cursor-pointe  py-3 text-dark pl-8 mt-5'
        >
          <FaBuilding />
          <span className=''>Placements</span>
        </NavLink>
        {/* Navitem end */}
        {/* Navitem */}
        <div className='nav-item flex items-center absolute bottom-8 gap-4 hover:text-white cursor-pointer py-3 text-navtext pl-8 mt-3' onClick={()=>{logout()}}>
          <AiOutlineLogout />
          <span className='' >Logout</span>
        </div>
        {/* Navitem end */}
      </div>
    </div>
  );
};

export default Sidebar;
