import React from 'react';
import { Line } from 'react-chartjs-2';
import { pure } from 'recompose';

const LineChart = (props={cgpa:[]}) => {
  console.log(props);
  let labels = props.cgpa.map((item)=>{
    return(item.sem)
  })
  let data = props.cgpa.map((item)=>{
    return(item.percentage)
  })

  console.log(labels)
  return (
    <div className='test'>
      <Line
        height='85%'
        data={{
          labels: labels,
          datasets: [
            {
              label: 'CGPA',
              data: data,
              backgroundColor: ['#99A6FF'],
              borderColor: ['#3751FF'],
              borderWidth: 1,
            },
          ],
        }}
      />
    </div>
  );
};

export default pure(LineChart);
