import React, { useState, useEffect } from 'react';
//import Header from '../../../components/Header';
import { useHistory } from 'react-router-dom';
import { http } from '../../../http';
import { useStateValue } from '../../../store/stateProvider';
import FacultyHeader from '../../../components/FacultyHeader';
import {
  AiOutlineSortAscending,
  AiOutlineSortDescending,
} from 'react-icons/ai';

const ClassList = () => {
  const [{ login }, dispatch] = useStateValue();
  let [count, setCount] = useState(1);
  const [sortby, setSortby] = useState('student');
  const [sort, setSort] = useState('asc');

  const history = useHistory();
  let id = localStorage.getItem('facultyId');
  async function getclasslist(value,type) {
    return await http('post', `facultystudent/get/${type}?att=${value}`, {
      facultyId: id,
    });
  }
  const [update, setupdate] = useState('CGPA')
  const [class1, setClass] = useState([]);
  const [class2, setclass2] = useState([]);
  const [chngsym, setchngsym] = useState(5);
  const [mym, setmym] = useState(5);
  useEffect(() => {
    const handleeffect = async () => {
    try {
      let response = await getclasslist(update,sort);
      console.log(response.data);
      setClass(response.data);
      setclass2(response.data);
      //   search("fdf");
    } catch (err) {
      console.log(err.message);
    }}
    handleeffect();
  }, [chngsym,update,sort]);
 // let reg = '^RA[1-9]';
  const [search, setSearch] = useState('');

  function toTitleCase(str) {
    return str.replace(/\w\S*/g, function (txt) {
      return txt.charAt(0).toUpperCase() + txt.substr(1).toLowerCase();
    });
  }
  function searchfunct(event) {
    setSearch(event.target.value);
    console.log(event.target.value);
    let value = toTitleCase(event.target.value);
    let regex = new RegExp('^' + event.target.value);
    let regex1 = new RegExp('^' + value);
    let regnoex = new RegExp(event.target.value + '[A-Z]{0,1}[0-9]{0,13}$');
    let listof = class2;

    let newlist = [];
    newlist = listof.filter((item) => {
      if (item.name.match(regex) != null || item.name.match(regex1) != null) {
        console.log(item.name.match(regex));
        newlist.push(item);
        return item;
      } else if (item.registrationNumber.match(regnoex) != null) {
        // console.log(item.name.match(regex))
        newlist.push(item);
        return item;
      } else if (item.gender.match(regex) != null) {
        // console.log(item.name.match(regex))
        newlist.push(item);
        return item;
      }
    });

    console.log(newlist);
    //newlist = []
    setClass(newlist);
  }
  function sort1(field) {
    let listof = class1;

    listof.sort(function (a, b) {
      return b[field] < a[field];
    });
    console.log(listof, 'dhf');
    setClass(listof);
    setmym(8);
  }
  async function gotostudent(registration) {
    try {
      console.log(registration);
      let response = await http(
        'post',
        'faculty/access',
        { facultyId: id, reg: registration },
        false
      );
      console.log(response);
      let facultytoken = localStorage.getItem('token');
      localStorage.setItem('token', response.data.token);
      localStorage.setItem('user', 'student');
      localStorage.setItem('facultytoken', facultytoken);
      dispatch({
        type: 'SET_LOGIN',
        login: true,
      });
      history.push('/dashboard/overview');
    } catch (err) {
      console.log(err);
    }
  }
  return (
    <div>
      <FacultyHeader headline='Class List' />
      <div className='mx-5 mt-6'>
        <input
          value={search}
          onChange={searchfunct}
          placeholder='Search'
          type='text'
          className='rounded-lg px-5 py-4 w-full focus:outline-none focus:ring-2 focus:ring-primary mt-5 focus:border-transparent outline-none bg-white border-2 border-border'
        />
      </div>
      <div className='border-2 border-border bg-white rounded-lg mx-5 mt-5 flex px-6'>
        <table className='w-full text-left'>
          <tr className='border-b text-primary'>
            <th
              onClick={() => {
                setCount(count + 1);
                setSortby('student');
                count % 2 === 0 ? setSort('desc') : setSort('asc');
              }}
              className='py-4 cursor-pointer'
            >
              <div className='gap-2 flex items-center'>
                Student{' '}
                {sort === 'asc' ? (
                  <AiOutlineSortDescending />
                ) : (
                  <AiOutlineSortAscending />
                )}
              </div>
            </th>

            <th
              onClick={() => {
                setupdate('X')
              }}
              className='py-4 cursor-pointer'
            >
              <div className='gap-2 flex items-center'>
                CGPA
                {sort === 'asc' ? (
                  <AiOutlineSortDescending />
                ) : (
                  <AiOutlineSortAscending />
                )}{' '}
              </div>
            </th>
            <th
              onClick={() => {
                setCount(count + 1);
                setSortby('classX');
                setupdate('X')
                count % 2 === 0 ? setSort('desc') : setSort('asc');
              }}
              className='py-4 cursor-pointer'
            >
              <div className='gap-2 flex items-center'>
                Class X
                {sort === 'asc' ? (
                  <AiOutlineSortDescending />
                ) : (
                  <AiOutlineSortAscending />
                )}
              </div>
            </th>
            <th
              onClick={() => {
                setCount(count + 1);
                setSortby('classXII');
                setupdate('XII')
                count % 2 === 0 ? setSort('desc') : setSort('asc');
              }}
              className='py-4 cursor-pointer'
            >
              <div className='gap-2 flex items-center'>
                Class XII
                {sort === 'asc' ? (
                  <AiOutlineSortDescending />
                ) : (
                  <AiOutlineSortAscending />
                )}
              </div>
            </th>
            <th
              onClick={() => {
                setCount(count + 1);
                setSortby('gender');
                count % 2 === 0 ? setSort('desc') : setSort('asc');
              }}
              className='py-4 cursor-pointer'
            >
              <div className='gap-2 flex items-center'>
                Gender
                {sort === 'asc' ? (
                  <AiOutlineSortDescending />
                ) : (
                  <AiOutlineSortAscending />
                )}
              </div>
            </th>
           
          </tr>
          {class1.map((item) => {
            //let rep = mym;
            return (
              <tr className='border-b group hover:bg-tableHover'>
                <td className='py-4 relative flex gap-3'>
                  <div className='h-9 w-9 rounded-full bg-gray-700'><img src={item?.photograph} className='h-9 w-9 rounded-full'></img></div>
                  <div
                    className='text-left'
                    onClick={() => {
                      gotostudent(item.registrationNumber);
                    }}
                  >
                    <h1 className='font-semibold'>
                      {!!item.name ? item.name : ''}
                    </h1>
                    <p className='text-xs'>
                      {!!item.registrationNumber ? item.registrationNumber : ''}
                    </p>
                  </div>
                </td>

                <td className='py-4'>
                  <span>{!!item.CGPA ? item.CGPA : ''}</span>
                </td>
                <td className='py-4'>
                  <span>{!!item.X ? item.X : ''}</span>
                </td>
                <td className='py-4'>
                  <span>{!!item.XII ? item.XII : ''}</span>
                </td>

                <td className='py-4'>
                  <span>{!!item.gender ? item.gender : ''}</span>
                </td>
               
              </tr>
            );
          })}
        </table>
      </div>
    </div>
  );
};

export default ClassList;
