import React from 'react';

const Input = (props) => {
  return (
    <input
      name={props.name}
      placeholder={props.title}
      type={props.type}
      onChange={props.onChange}
      onBlur={props.onBlur}
      value={props.value}
      className='rounded-lg px-5 py-5 w-full focus:outline-none focus:ring-2 focus:ring-primary mt-6 focus:border-transparent outline-none bg-white border-2 border-border'
    />
  );
};

export default Input;
