import React, { useState } from 'react';

const Date = (props) => {
  const [date, setDate] = useState('text');
  console.log(date);
  return (
    <input
      placeholder={props.title}
      onClick={() => setDate('date')}
      type={date}
      name={props.name}
      onChange={props.onChange}
      value={props.value}
      className='rounded-md px-3 text-sm py-2 w-full focus:outline-none focus:ring-2 focus:ring-primary mt-5 focus:border-transparent outline-none bg-white border-2 border-border'
    />
  );
};

export default Date;
