import React, { useState, useEffect } from 'react';
import Avatar from '../../../components/Avatar';
import Header from '../../../components/Header';
import { http } from '../../../http';
import { useStateValue } from '../../../store/stateProvider';
import Modal from './Modals/Modal';

const Certifications = () => {
  const [modalType, setModalType] = useState('');
  const [{ modal }, dispatch] = useStateValue();
  async function getcerti() {
    return http('get', 'certifications/get', {}, true);
  }

  const handleModal = (item) => {
    dispatch({
      type: 'SET_MODAL',
      modal: true,
    });
    setModalType(item);
  };
  const [certificates, setcertificates] = useState({
    intcertifications: [],
    internships: [],
  });

  // useEffect(async () => {
  //   try{
  //     let response = await getcerti();
  //     console.log(response.data);
  //     setcertificates(response.data);
  //   } catch(err){
  //     console.log(err);
  //   }
  // }, [chng])

  const handleFetch = async () => {
    try {
      let response = await getcerti();
      console.log(response.data);
      setcertificates(response.data);
    } catch (err) {
      console.log(err);
    }
  };

  useEffect(() => {
    dispatch({
      type: 'SET_MODAL',
      modal: false,
    });
  }, []);

  console.log('modal:', modal);

  useEffect(() => {
    setTimeout(() => {
      handleFetch();
    }, 1000);
  }, [modal]);

  return (
    <div>
      <Header headline='Certifications' type='student' />
      {modal && <Modal type={modalType} />}
      <div className='border-2 border-border bg-white px-6 rounded-lg mx-5 mt-12'>
        <div className='flex justify-between py-5'>
          <div>
            <h1 className='font-bold text-lg'>Certifications</h1>
            <p className='text-dark text-sm'>Enter your certificate details</p>
          </div>
          <p
            className='cursor-pointer text-primary font-semibold'
            onClick={() => handleModal('certifications')}
          >
            Add Entry
          </p>
        </div>
        <table className='w-full text-left'>
          <tr className='border-b text-primary'>
            <th className='py-4'>Certification</th>
            <th className='py-4'>Date of Issue</th>
            <th className='py-4'>Credential ID</th>
            <th className='py-4'>Certificate Link</th>
          </tr>
          {certificates.intcertifications.map((item) => {
            return (
              <tr className='border-b group hover:bg-tableHover'>
                <td className='py-4 relative flex gap-3'>
                  <Avatar name={!!item.title ? item.title[0] : ''} />
                  <div className='text-left'>
                    <h1 className='font-semibold'>
                      {!!item.title ? item.title : ''}
                    </h1>
                    <p className='text-xs'>{!!item.start ? item.start : ''}</p>
                  </div>
                </td>
                <td className='py-4 relative'>
                  <span>{!!item.issued ? item.issued : ''}</span>
                </td>
                <td className='py-4'>
                  <span>{!!item.credentialid ? item.credentialid : ''}</span>
                </td>
                <td className='py-4'>
                  <a href={!!item.link ? item.link : ''}>
                    <span className='bg-yellow-100 cursor-pointer px-5 text-yellow-600 py-2 text-xs rounded-full'>
                      View Certificate
                    </span>
                  </a>
                </td>
              </tr>
            );
          })}
        </table>
      </div>

      {/* internships */}
      <div className='border-2 border-border bg-white px-6 rounded-lg mx-5 my-12'>
        <div className='flex justify-between py-5'>
          <div>
            <h1 className='font-bold text-lg'>Internships</h1>
            <p className='text-dark text-sm'>
              Enter your internship experiences
            </p>
          </div>
          <p
            className='cursor-pointer text-primary font-semibold'
            onClick={() => handleModal('internships')}
          >
            Add Entry
          </p>
        </div>
        <table className='w-full text-left'>
          <tr className='border-b text-primary'>
            <th className='py-4'>Organisation</th>
            <th className='py-4'>Start Date</th>
            <th className='py-4'>End Date</th>
            <th className='py-4'>Duration</th>
            <th className='py-4'>Stipened</th>
          </tr>
          {certificates.internships.map((item) => {
            return (
              <tr className='border-b group hover:bg-tableHover'>
                <td className='py-4 relative flex gap-3'>
                  <Avatar name='A' />
                  <div className='text-left'>
                    <h1 className='font-semibold'>
                      {!!item.organisation ? item.organisation : ''}
                    </h1>
                    <p className='text-xs'>{!!item.role ? item.role : ''}</p>
                  </div>
                </td>
                <td className='py-4 relative'>
                  <span>{!!item.start ? item.start : ''}</span>
                </td>
                <td className='py-4'>
                  <span>{!!item.end ? item.end : ''}</span>
                </td>
                <td className='py-4'>
                  <span>{!!item.duration ? item.duration : ''}</span>
                </td>
                <td className='py-4'>
                  <span>{!!item.stipened ? item.stipened : ''}</span>
                </td>
              </tr>
            );
          })}
        </table>
      </div>
    </div>
  );
};

export default Certifications;
