import React, { useState, useEffect } from 'react';
import Card from '../../../components/Card';
import FacultyHeader from '../../../components/FacultyHeader';
import Header from '../../../components/Header';
import { http } from '../../../http';

const Overview = () => {
  const [edit, setEdit] = useState(false);
  const [facultydata, setFacultydata] = useState({});
  const [count, setCount] = useState(0);
  async function getpersonaldetails() {
    let id = localStorage.getItem('facultyId');
    return http('post', 'faculty/get', { facultyId: id }, true);
  }
  async function getregistercount() {
    let id = localStorage.getItem('facultyId');
    return http('post', 'registered/get', { facultyId: id }, true);
  }
  const handleFetch = async () => {
    try {
      let response = await getpersonaldetails();
      console.log(response);
      let count1 = await getregistercount();
      setCount(count1.data.count);
      setFacultydata(response.data);
    } catch (err) {
      console.log(err.message);
    }
  };
  // useEffect(async () => {
  //   try{
  //     let response = await getpersonaldetails();
  //     console.log(response);
  //     let count1 = await getregistercount();
  //     setCount(count1.data.count);
  //     setFacultydata(response.data);
  //   } catch(err){
  //     console.log(err.message);
  //   }

  // }, [])

  useEffect(() => {
    handleFetch();
  }, []);

  return (
    <div>
      <FacultyHeader headline='Overview' />
      {/* cards section */}
      <div className='flex gap-8 px-5 mt-10'>
        <Card width='1/3' title='Registered Students' value={count} />
        <Card width='1/3' title='Pending Verifications' value='50' />
        <Card
          width='1/3'
          title='CLass Strength'
          value={!!facultydata.totalstudents ? facultydata.totalstudents : ''}
        />
      </div>
      {/* academic performance */}

      {/* other details */}
      <div className='flex mx-5 gap-10 my-10'>
        {/* class details */}
        <div className='w-full border-2 bg-white border-border rounded-lg'>
          <div className='py-5 px-6 flex justify-between'>
            <div>
              <h1 className='font-bold text-lg'>Class Details</h1>
              <p className='text-dark text-sm'>Your home to success</p>
            </div>
            {/* <h1 className='text-primary cursor-pointer font-semibold'>
              Contact
            </h1> */}
          </div>
          <div className='w-full flex justify-between px-8 py-4 border-b border-border'>
            <p className='text-sm'>Department</p>
            <p className='text-sm'>
              {!!facultydata.department ? facultydata.department : ''}
            </p>
          </div>
          <div className='w-full flex justify-between px-8 py-4 border-b border-border'>
            <p className='text-sm'>Specialization</p>
            <p className='text-sm'>
              {!!facultydata.specilization ? facultydata.specilization : ''}
            </p>
          </div>
          <div className='w-full flex justify-between px-8 py-4 border-b border-border'>
            <p className='text-sm'>Section</p>
            <p className='text-sm'>
              {!!facultydata.section ? facultydata.section : ''}
            </p>
          </div>
        </div>

        {/* personal details */}
        <div className='w-full border-2 bg-white border-border rounded-lg'>
          <div className='py-5 px-6 flex justify-between'>
            <div>
              <h1 className='font-bold text-lg'>Personal Details</h1>
              <p className='text-dark text-sm'>Required for communication</p>
            </div>
            {edit ? (
              <h1
                className='text-primary cursor-pointer font-semibold'
                onClick={() => setEdit(!edit)}
              >
                Save Details
              </h1>
            ) : (
              <h1
                className='text-primary cursor-pointer font-semibold'
                onClick={() => setEdit(!edit)}
              >
                Edit Details
              </h1>
            )}
          </div>
          <div className='w-full flex justify-between px-8 py-4 border-b border-border'>
            <p className='text-sm'>Email</p>
            {edit ? (
              <input
                className='text-center text-xs bg-transparent focus:outline-none'
                placeholder='Enter Email'
              />
            ) : (
              <span className='text-sm'>
                {!!facultydata.email ? facultydata.email : ''}
              </span>
            )}
          </div>
          <div className='w-full flex justify-between px-8 py-4 border-b border-border'>
            <p className='text-sm'>Contact</p>
            {edit ? (
              <input
                className='text-center text-xs bg-transparent focus:outline-none'
                placeholder='Enter Email'
              />
            ) : (
              <span className='text-sm'>
                {!!facultydata.number ? facultydata.number : ''}
              </span>
            )}
          </div>
          <div className='w-full flex justify-between px-8 py-4 border-b border-border'>
            <p className='text-sm'>Faculty ID</p>
            {edit ? (
              <input
                className='text-center text-xs bg-transparent focus:outline-none'
                placeholder='Enter Contact'
              />
            ) : (
              <span className='text-sm'>
                {!!facultydata.facultyId ? facultydata.facultyId : ''}
              </span>
            )}
          </div>
        </div>
      </div>
    </div>
  );
};

export default Overview;
