import React, { useState ,useEffect} from 'react';
import logo from '../assets/logo.png';
import Button from '../components/Button';
import Input from '../components/Input';
import { useFormik } from 'formik';
//import Select from '../components/Select';
import { useHistory } from 'react-router-dom';
import { http } from '../http';

const Register = () => {
  const history = useHistory();
  const validate = (values) => {
    const errors = {};
    if (!values.name) {
      errors.name = 'Required';
    }

    if (!values.personalemail) {
      errors.personalemail = 'Required';
    }

    if (!values.parentEmail) {
      errors.parentEmail = 'Required';
    }

    if (!values.email) {
      errors.email = 'Required';
    }  else if (!/^[a-z]{2}[0-9]{4}@srmist.edu.in$/i.test(values.email)) {
      errors.email = 'Invalid College email';
    }

    if (!values.number) {
      errors.number = 'Required';
    }

    if (!values.dept) {
      errors.dept = 'Required';
    }

    if (!values.specilization) {
      errors.specilization = 'Required';
    }

    if (!values.gender) {
      errors.gender = 'Required';
    }

    if (!values.parentContact) {
      errors.parentContact = 'Required';
    }

    if (!values.section) {
      errors.section = 'Required';
    }

    if (!values.faculty) {
      errors.faculty = 'Required';
    }

    // if (!values.photograph) {
    //   errors.photograph = 'Required';
    // }

    if (!values.xmarks) {
      errors.xmarks = 'Required';
    }

    if (!values.xiimarks) {
      errors.xiimarks = 'Required';
    }

    if (!values.arrears) {
      errors.arrears = 'Required';
    }

    if (!values.standingarrears) {
      errors.standingarrears = 'Required';
    }

    if (!values.github) {
      errors.github = 'Required';
    }else if (!/https?:\/\/(www\.)?[-a-zA-Z0-9@:%._\+~#=]{1,256}\.[a-zA-Z0-9()]{1,6}\b([-a-zA-Z0-9()@:%_\+.~#?&//=]*)/i.test(values.github)) {
      errors.github = 'Invalid link';
    }

    if (!values.registrationNumber) {
      errors.registrationNumber = 'Required';
    } else if (!/RA[0-9]{13}$/i.test(values.registrationNumber)) {
      errors.registrationNumber = 'Invalid Registration Number';
    }

    if (!values.password) {
      errors.password = 'Required';
    } else if (values.password.length < 8) {
      errors.password = 'Must be 8 characters or more';
    }

    if (!values.repassword) {
      errors.repassword = 'Required';
    } else if (values.repassword !== values.password) {
      errors.repassword = "Second password doesn't match";
    }

    return errors;
  };
  async function register(data) {
    return await http('post', 'students/register', data, false);
  }

  const [photo, setPhoto] = useState('');
let neobj;
const onFileChange = async (event) => {
    // Update the state
    console.log(event.target.files[0]);
    neobj = { selectedFile: event.target.files[0] };
    console.log((selectedfile)=>{return neobj});
    await setSelectedfile( event.target.files[0]);
    setTimeout(()=>{
    console.log(selectedfile);},1000)
  };
  const [register1, setregister1] = useState(false);
  const [selectedfile, setSelectedfile] = useState(false);
  useEffect(() => {
   
    if(selectedfile){
       console.log(selectedfile);
      setPhoto(true);
    } else {
      setPhoto(false);
    }
  }, [selectedfile])

  async function uploadfile(filename) {
    const formdata = new FormData();
    console.log(formik.values.registrationNumber);
    console.log(neobj);
    formdata.append(
      'certificates',
      selectedfile,
      formik.values.registrationNumber + '.jpg'
    );
    try {
      return await http('post', 'file/upload', formdata);
    } catch (err) {
      console.log(err.message);
    }
  }
  const formik = useFormik({
    initialValues: {
      name: '',
      registrationNumber: '',
      // password: '',
      personalemail: '',
      email: '',
      number: '',
      dept: '',
      specilization: '',
      // new
      gender: '',
      parentContact: '',
      parentEmail: '',
      password: '',
      repassword: '',
      section: '',
      faculty: '',
      xmarks: '',
      xiimarks: '',
      arrears: '',
      standingarrears: '',
      github: '',
      linkedIn: '',
      portfolio: '',
      photograph: '',
    },
    validate,

    onSubmit: async (values) => {
      if(photo){
      await uploadfile(values.registratioNumber);
      console.log(values);
      try {
        if (values.password !== values.reppassword) {
          let values1 = values;
          values1.educationDetails = {
            school: { X: { percentage: '' }, XII: { percentage: '' } },
          };
          values1.educationDetails.school.X.percentage = values.xmarks;
          values1.educationDetails.school.XII.percentage = values.xiimarks;
          values1.photograph = `https://firebasestorage.googleapis.com/v0/b/placement-1acb5.appspot.com/o/${values1.registrationNumber}.jpg?alt=media`;
          delete values1.xiimarks;
          delete values1.xmarks;
          //setPhoto(values1.photograph);
          let response = await register(values1);
          console.log(response);
          if (response.data.message === 'Register success') {
            setregister1(true);
            history.push('/mail');
          }
        } else {
          alert('Registration number has already beeen used')
          console.log('password do not match');
        }
      } catch (error) {
        alert(error.message);
        console.log(error);
      }
      //  try {
      //   let response =await login(values);
      //   console.log(response.data);
      //   localStorage.setItem('token',response.data.token);
      //   localStorage.setItem('user',response.data.user);
      //   } catch (error) {
      //        console.log(error);
      //     }
      //   console.log(JSON.stringify(values, null));
    }
  }});
  return (
    <div className='w-full back overflow-hidden bg-white'>
      <div className='header bg-side h-20 w-full bg-cover relative flex items-center'>
        <div className='absolute border-2 border-white h-16 w-16 top-1/2 left-24 rounded-full'>
          <a href='/'>
            {' '}
            <img className='w-full h-full' src={logo} alt='' />
          </a>
        </div>
        <h1 className='text-white text-2xl text-right w-full pr-16'>
          Student Registration
        </h1>
      </div>
      <div className='content w-10/12 my-28 rounded-lg border-2 border-border bg-light mx-auto py-16 px-24'>
        <form onSubmit={formik.handleSubmit}>
          <div>
            <h1 className='text-2xl font-medium'>Personal Details</h1>
            <div className='flex gap-12 mt-8 w-full'>
              <div className='w-full relative'>
                <Input
                  onBlur={formik.handleBlur}
                  onChange={formik.handleChange}
                  value={formik.values.name}
                  title='Name'
                  name='name'
                  type='text'
                />
                {formik.touched.name && formik.errors.name && (
                  <p className='absolute text-xs text-red-500 font-semibold mt-2 top-full left-0'>
                    *{formik.errors.name}
                  </p>
                )}
              </div>
              <div className='w-full relative'>
                <Input
                  onBlur={formik.handleBlur}
                  onChange={formik.handleChange}
                  value={formik.values.registrationNumber}
                  title='Registration No.'
                  name='registrationNumber'
                  type='text'
                />
                {formik.touched.registrationNumber &&
                  formik.errors.registrationNumber && (
                    <p className='absolute text-xs text-red-500 font-semibold mt-2 top-full left-0'>
                      *{formik.errors.registrationNumber}
                    </p>
                  )}
              </div>
            </div>
            <div className='flex gap-12 mt-5'>
              <div className='w-full relative'>
                <Input
                  onBlur={formik.handleBlur}
                  onChange={formik.handleChange}
                  value={formik.values.personalemail}
                  name='personalemail'
                  title='Personal Email'
                  type='email'
                />
                {formik.touched.personalemail &&
                  formik.errors.personalemail && (
                    <p className='absolute text-xs text-red-500 font-semibold mt-2 top-full left-0'>
                      *{formik.errors.personalemail}
                    </p>
                  )}
              </div>
              <div className='w-full relative'>
                <Input
                  onBlur={formik.handleBlur}
                  onChange={formik.handleChange}
                  value={formik.values.email}
                  name='email'
                  title='College Email'
                  type='email'
                />
                {formik.touched.email && formik.errors.email && (
                  <p className='absolute text-xs text-red-500 font-semibold mt-2 top-full left-0'>
                    *{formik.errors.email}
                  </p>
                )}
              </div>
            </div>
            <div className='flex gap-12 mt-5'>
              <div className='w-full relative'>
                <Input
                  onBlur={formik.handleBlur}
                  onChange={formik.handleChange}
                  value={formik.values.number}
                  name='number'
                  title='Contact Number'
                  type='number'
                />
                {formik.touched.number && formik.errors.number && (
                  <p className='absolute text-xs text-red-500 font-semibold mt-2 top-full left-0'>
                    *{formik.errors.number}
                  </p>
                )}
              </div>
              <div className='w-full relative'>
                <select
                  className='rounded-lg px-5 py-5 w-full focus:outline-none focus:ring-2 focus:ring-primary mt-5 focus:border-transparent outline-none text-dark bg-white border-2 border-border'
                  name='gender'
                  onChange={formik.handleChange}
                  value={formik.values.gender}
                  onBlur={formik.handleBlur}
                >
                  <option value='' className='text-black'>
                    Gender
                  </option>
                  <option className='text-black' value='female'>
                    Female
                  </option>
                  <option className='text-black' value='male'>
                    Male
                  </option>
                </select>
                {formik.touched.gender && formik.errors.gender && (
                  <p className='absolute text-xs text-red-500 font-semibold mt-2 top-full left-0'>
                    *{formik.errors.gender}
                  </p>
                )}
              </div>
            </div>
            <div className='flex gap-12 mt-5'>
              <div className='w-full relative'>
                <Input
                  onBlur={formik.handleBlur}
                  onChange={formik.handleChange}
                  value={formik.values.parentContact}
                  name='parentContact'
                  title="Parent's Contact"
                  type='number'
                />
                {formik.touched.parentContact &&
                  formik.errors.parentContact && (
                    <p className='absolute text-xs text-red-500 font-semibold mt-2 top-full left-0'>
                      *{formik.errors.parentContact}
                    </p>
                  )}
              </div>

              <div className='w-full relative'>
                <Input
                  onBlur={formik.handleBlur}
                  onChange={formik.handleChange}
                  value={formik.values.parentEmail}
                  name='parentEmail'
                  title="Parent's Email"
                  type='email'
                />
                {formik.touched.parentEmail && formik.errors.parentEmail && (
                  <p className='absolute text-xs text-red-500 font-semibold mt-2 top-full left-0'>
                    *{formik.errors.parentEmail}
                  </p>
                )}
              </div>
            </div>
            <div className='flex gap-12 mt-5'>
              <div className='w-full relative'>
                <Input
                  onBlur={formik.handleBlur}
                  onChange={formik.handleChange}
                  value={formik.values.password}
                  name='password'
                  title='Password'
                  type='password'
                />
                {formik.touched.password && formik.errors.password && (
                  <p className='absolute text-xs text-red-500 font-semibold mt-2 top-full left-0'>
                    *{formik.errors.password}
                  </p>
                )}
              </div>

              <div className='w-full relative'>
                <Input
                  onBlur={formik.handleBlur}
                  onChange={formik.handleChange}
                  value={formik.values.repassword}
                  name='repassword'
                  title='Confirm Password'
                  type='password'
                />
                {formik.touched.repassword && formik.errors.repassword && (
                  <p className='absolute text-xs text-red-500 font-semibold mt-2 top-full left-0'>
                    *{formik.errors.repassword}
                  </p>
                )}
              </div>
            </div>
          </div>

          <div className='mt-20'>
            <h1 className='text-2xl font-medium'>College Details</h1>
            <div className='flex gap-12 mt-8'>
              <div className='w-full relative'>
                <Input
                  onBlur={formik.handleBlur}
                  onChange={formik.handleChange}
                  value={formik.values.branch}
                  name='dept'
                  title='Branch'
                  type='text'
                />
                {formik.touched.dept && formik.errors.dept && (
                  <p className='absolute text-xs text-red-500 font-semibold mt-2 top-full left-0'>
                    *{formik.errors.dept}
                  </p>
                )}
              </div>

              <div className='w-full relative'>
                <Input
                  onBlur={formik.handleBlur}
                  onChange={formik.handleChange}
                  value={formik.values.specilization}
                  name='specilization'
                  title='Specialization'
                  type='text'
                />{' '}
                {formik.touched.specilization &&
                  formik.errors.specilization && (
                    <p className='absolute text-xs text-red-500 font-semibold mt-2 top-full left-0'>
                      *{formik.errors.specilization}
                    </p>
                  )}
              </div>
            </div>
            <div className='flex gap-12 mt-8'>
              <div className='w-full relative'>
                <Input
                  onBlur={formik.handleBlur}
                  onChange={formik.handleChange}
                  value={formik.values.section}
                  name='section'
                  title='Section'
                  type='text'
                />
                {formik.touched.section && formik.errors.section && (
                  <p className='absolute text-xs text-red-500 font-semibold mt-2 top-full left-0'>
                    *{formik.errors.section}
                  </p>
                )}
              </div>

              <div className='w-full relative'>
                <select
                  className='rounded-lg px-5 py-5 w-full focus:outline-none focus:ring-2 focus:ring-primary mt-5 focus:border-transparent outline-none text-dark bg-white border-2 border-border'
                  name='faculty'
                  onChange={formik.handleChange}
                  value={formik.values.faculty}
                  onBlur={formik.handleBlur}
                >
                  <option value='' className='text-black'>
                    Faculty Advisor
                  </option>
                  <option className='text-black' value='KC Prabhushankar'>
                    KC Prabushankar
                  </option>
                </select>
                {formik.touched.faculty && formik.errors.faculty && (
                  <p className='absolute text-xs text-red-500 font-semibold mt-2 top-full left-0'>
                    *{formik.errors.faculty}
                  </p>
                )}
              </div>
            </div>
          </div>

          <div className='mt-20'>
            <h1 className='text-2xl font-medium'>Educational Details</h1>
            <div className='flex gap-12 mt-8'>
              <div className='w-full relative'>
                <Input
                  onBlur={formik.handleBlur}
                  onChange={formik.handleChange}
                  value={formik.values.xmarks}
                  name='xmarks'
                  title='Class X Marks (in %)'
                  type='number'
                />
                {formik.touched.xmarks && formik.errors.xmarks && (
                  <p className='absolute text-xs text-red-500 font-semibold mt-2 top-full left-0'>
                    *{formik.errors.xmarks}
                  </p>
                )}
              </div>

              <div className='w-full relative'>
                <Input
                  onBlur={formik.handleBlur}
                  onChange={formik.handleChange}
                  value={formik.values.xiimarks}
                  name='xiimarks'
                  title='Class XII Marks (in %)'
                  type='number'
                />
                {formik.touched.xiimarks && formik.errors.xiimarks && (
                  <p className='absolute text-xs text-red-500 font-semibold mt-2 top-full left-0'>
                    *{formik.errors.xiimarks}
                  </p>
                )}
              </div>
            </div>
            <div className='flex gap-12 mt-5'>
              <div className='relative w-full'>
                <Input
                  onBlur={formik.handleBlur}
                  onChange={formik.handleChange}
                  value={formik.values.backlogs}
                  name='arrears'
                  title='Total Backlogs'
                  type='text'
                />
                {formik.touched.arrears && formik.errors.arrears && (
                  <p className='absolute text-xs text-red-500 font-semibold mt-2 top-full left-0'>
                    *{formik.errors.arrears}
                  </p>
                )}
              </div>

              <div className='relative w-full'>
                <Input
                  onBlur={formik.handleBlur}
                  onChange={formik.handleChange}
                  value={formik.values.standingBacklogs}
                  name='standingarrears'
                  title='Standing Backlogs'
                  type='text'
                />
                {formik.touched.standingarrears &&
                  formik.errors.standingarrears && (
                    <p className='absolute text-xs text-red-500 font-semibold mt-2 top-full left-0'>
                      *{formik.errors.standingarrears}
                    </p>
                  )}
              </div>
            </div>
          </div>

          <div className='mt-20'>
            <h1 className='text-2xl font-medium'>Social Links</h1>
            <div className='mt-8 relative'>
              <Input
                onBlur={formik.handleBlur}
                onChange={formik.handleChange}
                value={formik.values.github}
                name='github'
                title='Github'
                type='text'
              />
              {formik.touched.github && formik.errors.github && (
                <p className='absolute text-xs text-red-500 font-semibold mt-2 top-full left-0'>
                  *{formik.errors.github}
                </p>
              )}
            </div>
            <div className='mt-8 relative'>
              <Input
                onBlur={formik.handleBlur}
                onChange={formik.handleChange}
                value={formik.values.linkedIn}
                name='linkedIn'
                title='LinkedIn'
                type='text'
              />
            </div>
            <div className='mt-8'>
              <Input
                onBlur={formik.handleBlur}
                onChange={formik.handleChange}
                value={formik.values.portfolio}
                name='portfolio'
                title='Portfolio'
                type='text'
              />
            </div>
          </div>

          <div className='mt-20'>
            <h1 className='text-2xl font-medium'>Photograph</h1>
            <div className='mt-8 w-1/2 relative'>
              <Input
                onChange={(event)=>onFileChange(event)}
                name='photograph'
                title='Your recent photograph'
                type='file'
                id='fileinput'
                
              />
            
              {!photo? (
                <p className='absolute text-xs text-red-500 font-semibold mt-2 top-full left-0'>
                  *Photograph is Required
                </p>
              ):''}
            </div>
          </div>
          <div className='text-center mt-8'>
            <Button name='Submit' type="submit"/>
          </div>
        </form>
      </div>
    </div>
  );
};

export default Register;
