export const initialState = {
  user: {}, // user logeged in or not, auth
  modal: false,
  login: false, // modal
  fetch: false,
};

function reducer(state, action) {
  switch (action.type) {
    case 'SET_USER':
      return {
        ...state,
        user: action.user,
      };

    case 'SET_MODAL':
      return {
        ...state,
        modal: action.modal,
      };
    case 'SET_LOGIN':
      return {
        ...state,
        login: action.login,
      };
    case 'SET_FETCH':
      return {
        ...state,
        fetch: action.fetch,
      };

    default:
      return state;
  }
}
export default reducer;
