import React, { useState } from 'react';
import { FiX } from 'react-icons/fi';
import Date from '../../../../components/Date';
import ModalInput from '../../../../components/ModalInput';
import { http } from '../../../../http';
import { useStateValue } from '../../../../store/stateProvider';

const HackathonModal = () => {
  const handleClick = () => {
    dispatch({
      type: 'SET_MODAL',
      modal: false,
    });
  };

  const handleSubmit = (value) => {
    dispatch({
      type: 'SET_MODAL',
      modal: false,
    });
    addhackathons(value);
    console.log(hackathon);
  };

  const [{ modal, fetch }, dispatch] = useStateValue();
  const [hackathon, setHackathon] = useState({
    name: '',
    position: '',
  });

  const handleHackathonChange = (event) => {
    const { value, name } = event.target;

    setHackathon((prev) => {
      if (name === 'name') {
        return {
          ...prev,
          name: value,
        };
      } else if (name === 'position') {
        return {
          ...prev,
          position: value,
        };
      }
    });
  };

  async function addhackathons(placement) {
    try {
      console.log(placement);
      let response = await http(
        'post',
        'hackathons/add',
        { ...placement, verified: 'pending' },
        true
      );
      handleClick();
    } catch (err) {
      console.log(err);
    }
  }

  return (
    <div className=' w-1/3 bg-white relative p-8'>
      <FiX
        onClick={() => handleClick()}
        className='font-semibold absolute top-4 text-xl cursor-pointer right-4'
      />
      <h1 className='font-bold text-lg'>Hackathon Details</h1>
      <p className='text-dark text-sm'>Please add all information carefully</p>
      <div className='flex gap-3'>
        <ModalInput
          name='name'
          value={hackathon.name}
          onChange={handleHackathonChange}
          title='Title'
          type='text'
        />
        <ModalInput
          name='position'
          value={hackathon.position}
          onChange={handleHackathonChange}
          title='Position'
          type='text'
        />
      </div>
      <div className='text-center'>
        <button
          onClick={() => {
            handleSubmit(hackathon);
          }}
          className='bg-primary text-sm font-semibold focus:outline-none delay-75 ease-linear duration-100 transform hover:-translate-y-1 outline-none text-white rounded-lg px-10 py-2 mt-8'
        >
          Submit
        </button>
      </div>
    </div>
  );
};

export default HackathonModal;
