import React, { useState, useEffect } from 'react';
import { AiFillGithub, AiFillLinkedin } from 'react-icons/ai';
import { FiGlobe } from 'react-icons/fi';
import avatar from '../assets/avatar.jpg';
import { http } from '../http';

const Header = (props) => {
  const [person, setPerson] = useState({});

  async function getuserdetails() {
    let token = localStorage.getItem('token');
    return await http('get', 'userdetail/get', {}, true);
  }

  const handleFetch = async () => {
    let ac = new AbortController();
    try {
      let response = await getuserdetails();
      setPerson(response.data);
    } catch (err) {
      console.log(err);
    }
    ac.abort();
  };

  console.log('person', person);
  useEffect(() => {
    handleFetch();
  }, []);

  return (
    <div className='flex items-center h-16 justify-between py-4'>
      <h1 className='text-xl font-bold'>{props.headline}</h1>
      <div className='flex items-center h-full gap-3 pl-6 border-l-2 border-border relative'>
        {props.type === 'student' && (
          <div className='absolute text-xl text-dark flex gap-5 -left-36'>
            {person.portfolio!=''?(<a href={person.portfolio}><FiGlobe className='cursor-pointer hover:text-primary duration-150' /></a>):''}
           { person.github?(<a href={person.github}><AiFillGithub className='cursor-pointer hover:text-primary duration-150' /></a>):''}
            {person.linkedin!=''?(<a href={person.portfolio}><AiFillLinkedin className='cursor-pointer hover:text-primary duration-150' /></a>):''}
          </div>
        )}
        <div>
          <span className='font-semibold'>{person.name}</span>
          {props.type === 'student' && (
            <p className='text-xs text-dark'>{person.registrationNumber}</p>
          )}
        </div>

        <div className='h-8 w-8 overflow-hidden rounded-full bg-gray-500 flex justify-center items-center'>
          {avatar ? (
            <img
              src={person.photograph}
              className='h-11/12 w-11/12 rounded-full object-contain'
              alt='avatar'
            />
          ) : (
            <span className='text-sm font-bold text-white'>A</span>
          )}
        </div>
      </div>
    </div>
  );
};

export default Header;
