import React from 'react';
import nf from './assets/nf.png';
import { BiArrowBack } from 'react-icons/bi';

const Page404 = () => {
  return (
    <div className='h-screen flex justify-center gap-16 items-center'>
      <div>
        <p className='text-4xl font-bold'>Our dog ate this page.</p>
        <p className='text-2xl font-semibold mt-3 text-dark'>
          There is nothing left here :(
        </p>
        <a href='/'>
          <div className='flex text-primary bold mt-5 items-center gap-3 cursor-pointer'>
            <BiArrowBack />
            <span>Back to home</span>
          </div>
        </a>
      </div>
      <img src={nf} className='w-3/12' alt='' />
    </div>
  );
};

export default Page404;
