import React from 'react';
import sent from '../assets/sent.gif';

const Email = () => {
  return (
    <div className='h-screen w-screen text-center'>
      <img src={sent} className='mx-auto z-0' alt='sent'></img>
      <h1 className='text-2xl font-semibold z-40 -mt-36'>
        We have sent you a mail
      </h1>
      <p className='text-dark mt-5'>Check your mail and</p>
      <p className='text-dark'>
        come back here to dive into the awesome world of Metier.
      </p>
      <a href='/'>
        <p className='text-primary cursor-pointer mt-3 font-semibold'>
          Go back to home
        </p>
      </a>
    </div>
  );
};

export default Email;
