import React, { useEffect, useState } from 'react';
import FacultyHeader from '../../../components/FacultyHeader';
import Header from '../../../components/Header';
import { http } from '../../../http';
//import {get12,get10,getplacements,getcollege,verifyten,verifytwelve,verifycollege,verifyplacement} from './verficationservice';

const Verification = () => {
  let id = localStorage.getItem('facultyId');
  async function get10() {
    return http('post', 'tenth/get', { facultyId: id }, true);
  }
  async function get12() {
    return http('post', 'twelve/get', { facultyId: id }, true);
  }
  async function getplacements() {
    return http('post', 'placement/get', { facultyId: id }, true);
  }
  async function getcollege() {
    return http('post', 'collegesem/get', { facultyId: id }, true);
  }
  async function verifyten(data) {
    return http('post', 'ten/verify', { ...data, facultyId: id });
  }
  async function verifytwelve(data) {
    return http('post', 'twelve/verify', { ...data, facultyId: id });
  }
  async function verifyplacement(data) {
    return http('post', 'placement/verify', { ...data, facultyId: id });
  }
  async function verifycollege(data) {
    return http('post', 'college/verify', { ...data, facultyId: id });
  }

  const [twelve, settwelve] = useState([]);
  const [tenth, settenth] = useState([]);
  const [placement, setPlacement] = useState([]);
  const [colleg, setColleg] = useState([]);
  const [changetend, setChangetend] = useState(5);
  const [chngcoll, setchngcoll] = useState(5);
  useEffect(() => {
    const handleChange = async () => {
      try {
        let resonse = await get10();
        console.log(resonse.data);
        let resonse1 = await get12();
        console.log(resonse1.data);
        let resonse_placement = await getplacements();
        console.log(resonse_placement.data);
        let resonse_college = await getcollege();
        console.log(resonse_college.data);
        setColleg(resonse_college.data);
        settwelve(resonse1.data);
        setPlacement(resonse_placement.data);
        settenth(resonse.data);
      } catch (err) {
        console.log(err.message);
      }
    };

    handleChange();
  }, [changetend, chngcoll]);

  return (
    <div>
      <FacultyHeader headline='Verification' />
      <div className='flex mx-5 gap-10 my-10'>
        {/* class details */}
        <div className='w-full border-2 bg-white border-border rounded-lg'>
          <div className='py-5 px-6 flex justify-between'>
            <div>
              <h1 className='font-bold text-lg'>Class X</h1>
              <p className='text-dark text-xs'>
                Verification for class X marks
              </p>
            </div>
          </div>
          {tenth.map((item) => {
            return (
              <div className='w-full flex justify-between px-8 py-4 items-center border-b border-border'>
                <div className='relative flex gap-3 text-xs'>
                  <div className='h-9 w-9 rounded-full bg-gray-700'></div>
                  <div className='text-left'>
                    <h1 className='font-semibold'>
                      {!!item.name ? item.name : ''}
                    </h1>
                    <p className='text-xs'>{!!item.reg ? item.reg : ''}</p>
                  </div>
                </div>
                <p className='text-xs'>
                  {!!item.percentage ? item.percentage : ''}
                </p>
                <div>
                  <span
                    className='bg-red-100 cursor-pointer px-5 duration-150 hover:bg-red-200 text-red-600 py-2 text-xs rounded-full'
                    onClick={async () => {
                      try {
                        console.log('clicked');
                        let response = await verifyten({
                          reg: item.reg,
                          response: 'rejected',
                        });
                        console.log(response);
                        setChangetend(8);
                      } catch (err) {
                        console.log(err.message);
                      }
                    }}
                  >
                    Reject
                  </span>
                  <span
                    className='bg-green-100 cursor-pointer ml-2 px-5 duration-150 hover:bg-green-200 text-green-800 py-2 text-xs rounded-full'
                    onClick={async () => {
                      try {
                        console.log('clicked');
                        let response = await verifyten({
                          reg: item.reg,
                          response: 'accepted',
                        });
                        console.log(response);
                        setChangetend(8);
                      } catch (err) {
                        console.log(err.message);
                      }
                    }}
                  >
                    Verify
                  </span>
                </div>
              </div>
            );
          })}
        </div>

        {/* personal details */}

        <div className='w-full border-2 bg-white border-border rounded-lg'>
          <div className='py-5 px-6 flex justify-between'>
            <div>
              <h1 className='font-bold text-lg'>Class XII</h1>
              <p className='text-dark text-xs'>
                Verification for class X marks
              </p>
            </div>
          </div>
          {twelve.map((item) => {
            return (
              <div className='w-full flex justify-between px-8 py-4 items-center border-b border-border'>
                <div className='relative flex gap-3 text-xs'>
                  <div className='h-9 w-9 rounded-full bg-gray-700'></div>
                  <div className='text-left'>
                    <h1 className='font-semibold'>{item.name}</h1>
                    <p className='text-xs'>{item.reg}</p>
                  </div>
                </div>
                <p className='text-xs'>{item.percentage}</p>
                <div>
                  <span
                    className='bg-red-100 cursor-pointer px-5 duration-150 hover:bg-red-200 text-red-600 py-2 text-xs rounded-full'
                    onClick={async () => {
                      try {
                        let response = await verifytwelve({
                          reg: item.reg,
                          response: 'rejected',
                        });
                      } catch (err) {
                        console.log(err.message);
                      }
                    }}
                  >
                    Reject
                  </span>
                  <span
                    className='bg-green-100 cursor-pointer ml-2 px-5 duration-150 hover:bg-green-200 text-green-800 py-2 text-xs rounded-full'
                    onClick={async () => {
                      try {
                        console.log('clicked');
                        let response = await verifytwelve({
                          reg: item.reg,
                          response: 'accepted',
                        });
                        console.log(response);
                      } catch (err) {
                        console.log(err.message);
                      }
                    }}
                  >
                    Verify
                  </span>
                </div>
              </div>
            );
          })}
        </div>
      </div>

      {/* Second row */}
      <div className='mx-5 gap-10 my-10'>
        {/* class details */}
        <div className='w-full border-2 bg-white border-border rounded-lg'>
          <div className='py-5 px-6 flex justify-between'>
            <div>
              <h1 className='font-bold text-lg'>CGPA</h1>
              <p className='text-dark text-xs'>Verification for CGPA</p>
            </div>
          </div>
          {colleg.map((item) => {
            return item.college.map((item1) => {
              return (
                <div className='w-full flex justify-between px-8 py-4 items-center border-b border-border'>
                  <div className='relative flex gap-3 text-xs'>
                    <div className='h-9 w-9 rounded-full bg-gray-700'></div>
                    <div className='text-left'>
                      <h1 className='font-semibold'>
                        {!!item.name ? item.name : ''}
                      </h1>
                      <p className='text-xs'>{!!item.reg ? item.reg : ''}</p>
                    </div>
                  </div>
                  <p className='text-xs'>{!!item1.sem ? item1.sem : ''}</p>
                  <p className='text-xs'>
                    {!!item1.percentage ? item1.percentage : ''}
                  </p>
                  <div>
                    <span
                      className='bg-red-100 cursor-pointer px-5 duration-150 hover:bg-red-200 text-red-600 py-2 text-xs rounded-full'
                      onClick={async () => {
                        try {
                          console.log('clicked');
                          setchngcoll(8);
                          let response = await verifycollege({
                            reg: item.reg,
                            response: 'rejected',
                            sem: item1.sem,
                          });
                          console.log(response);
                        } catch (err) {
                          console.log(err.message);
                        }
                      }}
                    >
                      Reject
                    </span>
                    <span
                      className='bg-green-100 cursor-pointer ml-2 px-5 duration-150 hover:bg-green-200 text-green-800 py-2 text-xs rounded-full'
                      onClick={async () => {
                        try {
                          console.log('clicked');
                          setchngcoll(8);
                          let response = await verifycollege({
                            reg: item.reg,
                            response: 'accepted',
                            sem: item1.sem,
                          });
                          console.log(response);
                        } catch (err) {
                          console.log(err.message);
                        }
                      }}
                    >
                      Verify
                    </span>
                  </div>
                </div>
              );
            });
          })}
        </div>

        {/* personal details */}

        <div className='w-full border-2 mt-8 bg-white border-border rounded-lg'>
          <div className='py-5 px-6 flex justify-between'>
            <div>
              <h1 className='font-bold text-lg'>Placements</h1>
              <p className='text-dark text-xs'>Verification for placements</p>
            </div>
          </div>
          {placement.map((item) => {
            
            return(item.placementDetails.map((item1)=>{
              console.log(item1);
              return(<div className='w-full flex justify-between px-8 py-4 items-center border-b border-border'>
                <div className='relative flex gap-3 text-xs'>
                  <div className='h-9 w-9 rounded-full bg-gray-700'></div>
                  <div className='text-left'>
                    <h1 className='font-semibold'>
                      {!!item.name ? item.name : ''}
                    </h1>
                    <p className='text-xs'>{!!item.registrationNumber ? item.registrationNumber : ''}</p>
                  </div>
                </div>
                <p className='text-xs'>{!!item1.company ? item1.company : ''}</p>
                <p className='text-xs'>{!!item1.status ? item1.status : ''}</p>
                <div>
                  <span
                    className='bg-red-100 cursor-pointer px-5 duration-150 hover:bg-red-200 text-red-600 py-2 text-xs rounded-full'
                    onClick={async () => {
                      try {
                        console.log('clicked');
                        let response = await verifyplacement({
                          reg: item.registrationNumber,
                          response: 'rejected',
                          company: item1.company,
                        });
                        console.log(response);
                        setChangetend(8);
                      } catch (err) {
                        console.log(err.message);
                      }
                    }}
                  >
                    Reject
                  </span>
                  <span
                    className='bg-green-100 cursor-pointer ml-2 px-5 duration-150 hover:bg-green-200 text-green-800 py-2 text-xs rounded-full'
                    onClick={async () => {
                      try {
                        console.log('clicked');
                        let response = await verifyplacement({
                          reg: item.registrationNumber,
                          response: 'accepted',
                          company: item1.company,
                        });
                        console.log(response);
                        setChangetend(8);
                      } catch (err) {
                        console.log(err.message);
                      }
                    }}
                  >
                    Verify
                  </span>
                </div>
              </div>
            );
          })
          )})}
          
            
        </div>
      </div>
    </div>
  );
};

export default Verification;
