import React from 'react';

const Card = (props) => {
  return (
    <div
      className={`w-${props.width} group hover:border-primary cursor-pointer duration-200 bg-white h-32  rounded-lg border-2 border-border flex items-center justify-center text-center`}
    >
      <div>
        <h1 className='text-dark font-semibold group-hover:text-primary duration-200'>
          {props.title}
        </h1>
        <p className='group-hover:text-primary text-4xl font-bold mt-3 duration-200'>
          {props.value}
        </p>
      </div>
    </div>
  );
};

export default Card;
