import React, { useState } from 'react';
import { FiX } from 'react-icons/fi';
import Date from '../../../../components/Date';
import ModalInput from '../../../../components/ModalInput';
import { http } from '../../../../http';
import { useStateValue } from '../../../../store/stateProvider';
import CodingContestModal from './CodingContestModal';
import HackathonModal from './HackathonModal';

const ProjectsModal = () => {
  const [{ modal, fetch }, dispatch] = useStateValue();
  const [newProjects, setNewProjects] = useState({
    name: '',
    techstack: '',
    description: '',
    link: '',
  });

  const handleClick = () => {
    dispatch({
      type: 'SET_MODAL',
      modal: false,
    });
  };

  const handleSubmit = (value) => {
    dispatch({
      type: 'SET_MODAL',
      modal: false,
    });
    addprojects(value);
  };

  async function addprojects(placement) {
    console.log(placement);
    try {
      let response = await http(
        'post',
        'projects/add',
        { ...placement, verified: 'pending' },
        true
      );
    } catch (err) {
      console.log(err);
    }
  }

  const handleProjectChange = (event) => {
    const { value, name } = event.target;

    setNewProjects((prev) => {
      if (name === 'name') {
        return {
          ...prev,
          name: value,
        };
      } else if (name === 'techstack') {
        return {
          ...prev,
          techstack: value,
        };
      } else if (name === 'description') {
        return {
          ...prev,
          description: value,
        };
      } else if (name === 'link') {
        return {
          ...prev,
          link: value,
        };
      }
    });
  };

  return (
    <div className=' w-1/3 bg-white relative p-8'>
      <FiX
        onClick={() => handleClick()}
        className='font-semibold absolute top-4 text-xl cursor-pointer right-4'
      />
      <h1 className='font-bold text-lg'>Project Details</h1>
      <p className='text-dark text-sm'>Please add all information carefully</p>
      <div className='flex gap-3'>
        <ModalInput
          name='name'
          onChange={handleProjectChange}
          title='Title'
          value={newProjects?.name}
          type='text'
        />
        <ModalInput
          onChange={handleProjectChange}
          value={newProjects?.techstack}
          title='Tech techstack'
          type='text'
          name='techstack'
        />
      </div>
      <div className='flex gap-3'>
        <ModalInput
          value={newProjects?.description}
          onChange={handleProjectChange}
          title='Description'
          type='text'
          name='description'
        />
        <ModalInput
          value={newProjects?.link}
          onChange={handleProjectChange}
          title='Link'
          type='text'
          name='link'
        />
      </div>
      <div className='text-center'>
        <button
          onClick={() => {
            handleSubmit(newProjects);
          }}
          className='bg-primary text-sm font-semibold focus:outline-none delay-75 ease-linear duration-100 transform hover:-translate-y-1 outline-none text-white rounded-lg px-10 py-2 mt-8'
        >
          Submit
        </button>
      </div>
    </div>
  );
};

export default ProjectsModal;
