import React, { useEffect, useState } from 'react';
import Header from '../../../components/Header';
import { RiEditCircleFill, RiSaveFill } from 'react-icons/ri';
import Modal from './Modals/Modal';
import ModalInput from '../../../components/ModalInput';
import { useStateValue } from '../../../store/stateProvider';
import avatar from '../../../assets/avatar.jpg';
import Avatar from '../../../components/Avatar';
import { http } from '../../../http';

const Placements = () => {
  const [edit, setEdit] = useState(false);
  const [modalType, setModalType] = useState('');
  const [{ modal }, dispatch] = useStateValue();
  const handleClick = (item) => {
    dispatch({
      type: 'SET_MODAL',
      modal: true,
    });
    setModalType(item);
  };
  async function getskills() {
    return http('get', 'skills/get', {}, true);
  }
  async function getsPlacement() {
    return http('get', 'getplacement', {}, true);
  }
  async function updatestatus(body,i){
    setEdit(!edit[i])
    return await http('post','placement/update',body,true);
  }
  function setedit(i){
    let newarr = Array(i).fill(false);
    newarr.push(!edit[i]);
    for(let m =i+1;i<placement.length;i++){
      newarr.push(false);
    }
    setEdit(newarr);
   
  }
  const [skills, setskills] = useState([]);
  const [placement, setPlacement] = useState([]);
  // useEffect(async () => {
  //   dispatch({
  //     type: 'SET_MODAL',
  //     modal: false,
  //   });
  //   try {
  //     let response = await getskills();
  //     console.log(response.data);
  //     setskills(response.data);
  //     let response1 = await getsPlacement();
  //     console.log(response1.data);
  //     setPlacement(response1.data);
  //   } catch (err) {
  //     console.log(err);
  //   }
  // }, []);
  const fetchData = async () => {
    try {
      let response = await getskills();
      console.log(response.data);
      setskills(response.data);
      let response1 = await getsPlacement();
      console.log(response1.data);
      setEdit(Array(response1.data.length).fill(false))
      setPlacement(response1.data);
    } catch (err) {
      console.log(err);
    }
  };
  const [status, setStatus] = useState('')
  const [updatenew, setUpdatenew] = useState(false)
  function handlestatus(event){
    setStatus(event.target.value);

  }

  useEffect(() => {
    setTimeout(() => {
      fetchData();
    }, 500);
  }, [modal,updatenew]);

  useEffect(() => {
    dispatch({
      type: 'SET_MODAL',
      modal: false,
    });
  }, []);

  return (
    <div>
      {modal && <Modal type={modalType} />}
      <Header headline='Placements' type='student' />
      <div className='border-2 border-border bg-white px-6 rounded-lg mx-5 mt-12'>
        <div className='flex justify-between py-5'>
          <div>
            <h1 className='font-bold text-lg'>Placement Opportunities</h1>
            <p className='text-dark text-sm'>Get a chance to grow and shine</p>
          </div>
          <p
            className='cursor-pointer text-primary font-semibold'
            onClick={() => handleClick('placement')}
          >
            Add Entry
          </p>
        </div>
        <table className='w-full text-left'>
          <tr className='border-b text-primary'>
            <th className='py-4'>Organisation</th>
            <th className='py-4'>Start Date</th>
            <th className='py-4'>Status</th>
            <th className='py-4'>Verification</th>
          </tr>
          {placement.map((item,i) => {
            return (
              <tr className='border-b group hover:bg-tableHover'>
                <td className='py-4 relative flex gap-3'>
                  <Avatar name={!!item.company ? item.company[0] : ''} />

                  <div className='text-left'>
                    <h1 className='font-semibold'>
                      {!!item.company ? item.company : ''}
                    </h1>
                    <p className='text-xs'>
                      {!!item.position ? item.position : ''}
                    </p>
                  </div>
                </td>
                <td className='py-4 relative'>
                  <span>{!!item.startdate ? item.startdate : ''}</span>
                </td>
                <td className='py-4'>
                  {edit[i] ? (
                    <input
                      className='text-center text-xs bg-transparent focus:outline-none'
                      placeholder='Enter updated status'
                      value={status}
                      onChange={handlestatus}
                    />
                  ) : (
                    <span>{!!item.status ? item.status : ''}</span>
                  )}
                </td>
                <td className='py-4 relative'>
                  {item.verified != 'pending' ? (
                    <span className='bg-green-100 px-5 text-green-800 py-2 text-xs rounded-full'>
                      Verified
                    </span>
                  ) : (
                    <span className='bg-red-100 px-5 text-red-800 py-2 text-xs rounded-full'>
                      Pending
                    </span>
                  )}

                  {!edit[i] ? (
                    <RiEditCircleFill
                      onClick={() => setedit(i)}
                      className='absolute top-1/2 text-xl opacity-0 transform -translate-y-1/2 right-5 cursor-pointer group-hover:opacity-60 text-primary'
                    />
                  ) : (
                    <RiSaveFill
                      onClick={async () =>{ try{await updatestatus({id:item._id,status:status},i); setUpdatenew(true);}catch(err){console.log(err)}}}
                      className='absolute text-xl top-1/2 transform -translate-y-1/2 right-5 cursor-pointer opacity-60 text-green-500'
                    />
                  )}
                </td>
              </tr>
            );
          })}
        </table>
      </div>
      <div className='border-2 border-border bg-white px-6 rounded-lg mx-5 my-12'>
        <div className='flex justify-between py-5'>
          <div>
            <h1 className='font-bold text-lg'>Skills</h1>
            <p className='text-dark text-sm'>Showcase what you got</p>
          </div>
          <p
            className='cursor-pointer text-primary font-semibold'
            onClick={() => handleClick('skills')}
          >
            Add Skill
          </p>
        </div>
        <div className='skills flex flex-wrap gap-3'>
          {skills.map((item) => {
            return (
              <span className='px-5 mb-5 py-2 text-xs bg-blue-200 text-blue-800 rounded-full'>
                {item.skill}
              </span>
            );
          })}
        </div>
      </div>
    </div>
  );
};

export default Placements;
