import React from 'react';
import Header from '../../../components/Header';
import Overview from './Overview';
import { BrowserRouter as Router, Switch, Route, Link } from 'react-router-dom';
import ClassList from './ClassList';
import Verification from './Verification';

const Main = () => {
  return (
    <div className='bg-light w-full pl-16 pr-6'>
      {/* <Overview /> */}
     
        <Switch>
          <Route exact path='/faculty/dashboard/overview'>
            <Overview />
          </Route>
          <Route exact path='/faculty/dashboard/classlist'>
            <ClassList />
          </Route>
          <Route exact path='/faculty/dashboard/verifications'>
            <Verification />
          </Route>
        </Switch>
  
    </div>
  );
};

export default Main;
