import React, { useState, useEffect } from 'react';
import Header from '../../../components/Header';
import {
  RiCheckboxCircleFill,
} from 'react-icons/ri';
import { FiAlertCircle } from 'react-icons/fi';
import { useStateValue } from '../../../store/stateProvider';

import Modal from './Modals/Modal';
import { http } from '../../../http';

const Education = () => {
  const [{ modal, user }, dispatch] = useStateValue();

  const verified = true;

  const handleEduDetails = () => {
    dispatch({
      type: 'SET_MODAL',
      modal: true,
    });
  };

  const [semester, setsemester] = useState([]);

  const [school, setschool] = useState({ X: '', XII: '' });
  const [arrears, setArrears] = useState(0);
  const [edit, setEdit] = useState(false);
async function update(data){
  return http('post','cgpa/update',data,true);
}
  async function geteducation() {
    //let token = localStorage.getItem('token');
    try {
      let response = await http('get', '/education/get', {}, true);
       let sum1 =  0;
      response.data.college.map((item) =>{
        sum1=sum1+parseInt(item.percentage);
      })
      sum1 =  sum1/response.data.college.length;
      setSum(sum1.toPrecision(3));
      setsemester(response.data.college);
      setschool(response.data.school);
      setArrears(response.data.arrears);
      await update({cgpa:sum1.toPrecision(3)});

      console.log(response);
    } catch (error) {
      console.log(error.message);
    }
  }
  const [sum, setSum] = useState(0)
  useEffect(() => {
    const ac = new AbortController();
    const handleeffect = async () =>{
    
    console.log(user, 'yes user');
    await geteducation();
      console.log(sum);
    
    }
    handleeffect();
    ac.abort();
  }, []);

  // useEffect(() => {
  //   dispatch({
  //     type: 'SET_MODAL',
  //     modal: false,
  //   });
  // }, []);

  return (
    <div className=''>
      {modal && <Modal type='education' marks ={semester}/>}
      <Header headline='Education' type='student' name={user.name} />
      <div className='border-2 border-border bg-white rounded-lg mx-5 mt-12 flex'>
        <div className='w-2/3 py-5 px-6 border-r-2 education_box border-border'>
          <div className='flex justify-between'>
            <div>
              <h1 className='font-bold text-lg'>Acadamic Performance</h1>
              <p className='text-dark text-sm'>Enter your cgpa details</p>
            </div>
            <p
              onClick={() => {
                handleEduDetails();
              }}
              className='cursor-pointer text-primary font-semibold'
            >
              Add Entry
            </p>
          </div>

          <div className='h-96 overflow-y-auto'>
            <table className='w-full text-center mt-8'>
              <tr className='border-b text-primary'>
                <th className='py-4'>Semester</th>
                <th className='py-4'>CGPA</th>
                <th className='py-4'>Verification</th>
              </tr>
              {semester.map((item) => {
                return (
                  <tr className='border-b group hover:bg-tableHover'>
                    <td className='py-4 relative'>
                      <span>{item.sem}</span>
                    </td>
                    <td className='py-4'>
                      {edit ? (
                        <input
                          className='text-center text-xs bg-transparent focus:outline-none'
                          placeholder='Enter CGPA'
                        />
                      ) : (
                        <span>{item.percentage}</span>
                      )}
                    </td>
                    <td className='py-4'>
                      {item.verified === 'pending' ? (
                        <span className='bg-red-100 px-5 text-red-600 py-2 text-xs rounded-full'>
                          Pending
                        </span>
                      ) : (
                        <span className='bg-green-100 px-5 text-green-800 py-2 text-xs rounded-full'>
                          Verified
                        </span>
                      )}
                    </td>
                  </tr>
                );
              })}
              {/* {!!semester.one?(<tr className='border-b group hover:bg-tableHover'>
                <td className='py-4 relative'>
                  <span>1</span>
                </td>
                <td className='py-4'>
                  {edit ? (
                    <input
                      className='text-center text-xs bg-transparent focus:outline-none'
                      placeholder='Enter CGPA'
                    />
                  ) : (
                    <span>9.8</span>
                  )}
                </td>
                <td className='py-4'>
                  {semester.one.verified == 'pending' ? (
                    <span className='bg-red-100 px-5 text-red-600 py-2 text-xs rounded-full'>
                      Pending
                    </span>
                  ) : (
                    <span className='bg-green-100 px-5 text-green-800 py-2 text-xs rounded-full'>
                      Verified
                    </span>
                  )}
                </td>
              </tr>):""}
              <tr className='border-b group hover:bg-tableHover'>
                <td className='py-4 relative'>
                  <span>2</span>
                </td>
                <td className='py-4'>
                  <span>{semester.two.percentage}</span>
                </td>
                <td className='py-4'>
                  {semester.two.verified == 'pending' ? (
                    <span className='bg-red-100 px-5 text-red-600 py-2 text-xs rounded-full'>
                      Pending
                    </span>
                  ) : (
                    <span className='bg-green-100 px-5 text-green-800 py-2 text-xs rounded-full'>
                      Verified
                    </span>
                  )}
                </td>
              </tr>
              <tr className='border-b group hover:bg-tableHover'>
                <td className='py-4 relative'>
                  <span>3</span>
                </td>
                <td className='py-4'>
                  <span>{semester.three.percentage}</span>
                </td>
                <td className='py-4'>
                  {semester.three.verified == 'pending' ? (
                    <span className='bg-red-100 px-5 text-red-600 py-2 text-xs rounded-full'>
                      Pending
                    </span>
                  ) : (
                    <span className='bg-green-100 px-5 text-green-800 py-2 text-xs rounded-full'>
                      Verified
                    </span>
                  )}
                </td>
              </tr>
              <tr className='border-b group hover:bg-tableHover'>
                <td className='py-4 relative'>
                  <span>4</span>
                </td>
                <td className='py-4'>
                  <span>{semester.four.percentage}</span>
                </td>
                <td className='py-4'>
                  {semester.four.verified == 'pending' ? (
                    <span className='bg-red-100 px-5 text-red-600 py-2 text-xs rounded-full'>
                      Pending
                    </span>
                  ) : (
                    <span className='bg-green-100 px-5 text-green-800 py-2 text-xs rounded-full'>
                      Verified
                    </span>
                  )}

                  {edit ? (
                    <RiSaveFill
                      onClick={() => setEdit(!edit)}
                      className='absolute top-1/2 transform -translate-y-1/2 right-3 text-green-500 cursor-pointer'
                    />
                  ) : (
                    <RiEditCircleFill
                      onClick={() => setEdit(!edit)}
                      className='absolute top-1/2 transform -translate-y-1/2 right-3 text-primary opacity-0 group-hover:opacity-60 duration-250 cursor-pointer'
                    />
                  )}
                </td>
              </tr> */}
            </table>
          </div>
        </div>
        <div className='w-1/3'>
          <div className='mt-5 pb-5 border-b-2 border-border relative'>
            {/* {!verified ? (
              <div className='absolute flex right-2 group top-0 text-yellow-600 gap-2 items-center text-xs'>
                <FiAlertCircle className='' />
                <span className=''>Pending</span>
              </div>
            ) : (
              <div className='absolute flex right-2 group top-0 text-green-600 gap-2 items-center text-xs'>
                <RiCheckboxCircleFill className='' />
                <span className=''>Verifed</span>
              </div>
            )} */}
            <h1 className='text-dark font-semibold text-center'>Class X</h1>
            <p className='text-center font-bold text-2xl'>
              {school.X.percentage}
            </p>
          </div>
          <div className='mt-5 pb-5 border-b-2 border-border relative'>
            {/* { school.X.verified==="pending"? (
              <div className='absolute flex right-2 group top-0 text-yellow-600 gap-2 items-center text-xs'>
                <FiAlertCircle className='' />
                <span className=''>Pending</span>
              </div>
            ) : (
              <div className='absolute flex right-2 group top-0 text-green-600 gap-2 items-center text-xs'>
                <RiCheckboxCircleFill className='' />
                <span className=''>Verifed</span>
              </div>
            )} */}
            <h1 className='text-dark font-semibold text-center'>Class XII</h1>
            <p className='text-center font-bold text-2xl'>
              {school.XII.percentage}
            </p>
          </div>
          <div className='mt-5 pb-5 border-b-2 border-border relative'>
            {/* {school.XII.verified==="pending" ? (
              <div className='absolute flex right-2 group top-0 text-yellow-600 gap-2 items-center text-xs'>
                <FiAlertCircle className='' />
                <span className=''>Pending</span>
              </div>
            ) : (
              <div className='absolute flex right-2 group top-0 text-green-600 gap-2 items-center text-xs'>
                <RiCheckboxCircleFill className='' />
                <span className=''>Verifed</span>
              </div>
            )} */}
            <h1 className='text-dark font-semibold text-center'>CGPA</h1>
            <p className='text-center font-bold text-2xl'>{sum}</p>
          </div>
          <div className='mt-5 pb-5 border-b-2 border-border'>
            <h1 className='text-dark font-semibold text-center'>
              Total Arears
            </h1>
            <p className='text-center font-bold text-2xl'>{arrears}</p>
          </div>
          <div className='mt-5 pb-5'>
            <h1 className='text-dark font-semibold text-center'>
              Standing Arears
            </h1>
            <p className='text-center font-bold text-2xl'>0</p>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Education;
