import React, { useState } from 'react';
import { FiX } from 'react-icons/fi';
import Date from '../../../../components/Date';
import ModalInput from '../../../../components/ModalInput';
import { http } from '../../../../http';
import { useStateValue } from '../../../../store/stateProvider';
import CodingContestModal from './CodingContestModal';
import HackathonModal from './HackathonModal';
import PlacementModal from './PlacementModal';
import ProjectsModal from './ProjectsModal';
import SkillModal from './SkillModal';

const CertificationsModal = () => {
  const [certification, setCertification] = useState({
    title: '',
    start: '',
    issued: '',
    credentialid: '',
    link: '',
  });

  const [{ modal }, dispatch] = useStateValue();

  const handleClick = () => {
    dispatch({
      type: 'SET_MODAL',
      modal: false,
    });
  };

  const handleSubmit = (value) => {
    dispatch({
      type: 'SET_MODAL',
      modal: !modal,
    });
    addcetificates(value);
  };

  async function addcetificates(placement) {
    try {
      let response = await http('post', 'courses/add', { ...placement }, true);
    } catch (err) {
      console.log(err);
    }
  }
  const handleCertificationChange = (event) => {
    const { value, name } = event.target;

    setCertification((prev) => {
      if (name === 'title') {
        return {
          title: value,
          start: prev.start,
          issued: prev.issued,
          credentialid: prev.credentialid,
          link: prev.link,
        };
      } else if (name === 'start') {
        return {
          ...prev,
          start: value,
        };
      } else if (name === 'issued') {
        return {
          ...prev,
          issued: value,
          credentialid: prev.credentialid,
        };
      } else if (name === 'credentialid') {
        return {
          ...prev,
          credentialid: value,
        };
      } else if (name === 'link') {
        return {
          ...prev,
          link: value,
        };
      }
    });
  };

  return (
    <div className=' w-1/3 bg-white relative p-8'>
      <FiX
        onClick={() => handleClick()}
        className='font-semibold absolute top-4 text-xl cursor-pointer right-4'
      />
      <h1 className='font-bold text-lg'>Certification Details</h1>
      <p className='text-dark text-sm'>Please add all information carefully</p>
      <ModalInput
        title='Title'
        value={certification.title}
        onChange={handleCertificationChange}
        type='text'
        name='title'
      />
      <div className='flex gap-3'>
        <ModalInput
          onChange={handleCertificationChange}
          title='Presented By'
          value={certification.start}
          type='text'
          name='start'
        />
        <Date
          onChange={handleCertificationChange}
          name='issued'
          value={certification.issued}
          title='Completed On'
        />
      </div>
      <div className='flex gap-3'>
        <ModalInput
          onChange={handleCertificationChange}
          name='credentialid'
          value={certification.credentialid}
          title='Credential ID'
          type='text'
        />
        <ModalInput
          name='link'
          value={certification.link}
          onChange={handleCertificationChange}
          title='Certificate Link'
          type='text'
        />
      </div>
      <div className='text-center'>
        <button
          onClick={() => {
            handleSubmit(certification);
          }}
          className='bg-primary text-sm font-semibold focus:outline-none delay-75 ease-linear duration-100 transform hover:-translate-y-1 outline-none text-white rounded-lg px-10 py-2 mt-8'
        >
          Submit
        </button>
      </div>
    </div>
  );
};

export default CertificationsModal;
