import React from 'react';
import { AiFillGithub, AiFillLinkedin } from 'react-icons/ai';
import { FiGlobe } from 'react-icons/fi';
import avatar from '../assets/avatar.jpg';
import { http } from '../http';

const FacultyHeader = (props) => {
  return (
    <div className='flex items-center h-16 justify-between py-4'>
      <h1 className='text-xl font-bold'>{props.headline}</h1>
      <div className='flex items-center h-full gap-3 pl-6 border-l-2 border-border relative'>
        {props.type === 'student' && (
          <div className='absolute text-xl text-dark flex gap-5 -left-36'>
            <FiGlobe className='cursor-pointer hover:text-primary duration-150' />
            <AiFillGithub className='cursor-pointer hover:text-primary duration-150' />
            <AiFillLinkedin className='cursor-pointer hover:text-primary duration-150' />
          </div>
        )}
        <div>
          <span className='font-semibold'>K C Prabhushankar</span>
        </div>

        <div className='h-8 w-8 overflow-hidden rounded-full bg-gray-500 flex justify-center items-center'>
          <span className='text-sm font-bold text-white'>K</span>
        </div>
      </div>
    </div>
  );
};

export default FacultyHeader;
