import React from 'react';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import StudentDashboard from './pages/Dashboard/Student';
import FacultyLogin from './pages/FacultuLogin';
import Login from './pages/Login';
import Register from './pages/Register';
import FacultyDashboard from './pages/Dashboard/Faculty/index';
//import Main from './pages/Dashboard/Student/Main';
import { useEffect, useState } from 'react';
import { useStateValue } from './store/stateProvider';
import Page404 from './Page404';
import { ProtectedRoute } from './ProtectedRoute';
import Email from './pages/Email';

function App() {
  const [{ login }, dispatch] = useStateValue();
  // const [loginnew, setLoginnew] = useState(false);
  // useEffect(() => {
  //   if (!!localStorage.getItem('token')) {
  //     setLoginnew(true);
  //   }

  //   console.log('yes');
  // }, []);
  return (
    <div className='App'>
      <Router>
        <Switch>
          <Route exact path='/'>
            <Login />
          </Route>
          <Route exact path='/faculty'>
            <FacultyLogin />
          </Route>
          <Route exact path='/register'>
            <Register />
          </Route>
          <ProtectedRoute
            path='/dashboard'
            // render={(props) =>
            //   !login ? <StudentDashboard /> : <Redirect to='/' />
            // }
            component={StudentDashboard}
            loggedIn={!!localStorage.getItem('token') || login}
          />

          {console.log('login', login)}

          <ProtectedRoute
            path='/faculty/dashboard'
            component={FacultyDashboard}
            loggedIn={!!localStorage.getItem('token') || login}
          />
          <Route exact path='/mail' component={Email} />
          <Route path='*' component={Page404} />
        </Switch>
      </Router>
    </div>
  );
}

export default App;
