import React, { useState } from 'react';
import { FiX } from 'react-icons/fi';
import Date from '../../../../components/Date';
import ModalInput from '../../../../components/ModalInput';
import { http } from '../../../../http';
import { useStateValue } from '../../../../store/stateProvider';
import CertificationsModal from './CertificationsModal';
import CodingContestModal from './CodingContestModal';
import HackathonModal from './HackathonModal';
import InternshipModal from './InternshipModal';
import PlacementModal from './PlacementModal';
import ProjectsModal from './ProjectsModal';
import SkillModal from './SkillModal';

const Modal = (props) => {
  const [{ modal, fetch }, dispatch] = useStateValue();

  const [marks, setMarks] = useState({
    sem: '',
    percentage: '',
  });
  const [cgpaerror, setCgpaerror] = useState(false)
  const [semerror, setSemerror] = useState(false)
  const handleMarksChange = (event) => {
    setSemerror(false);
    setCgpaerror(false);  
    const { value, name } = event.target;

    setMarks((prev) => {
      if (name === 'sem') {
        props.marks?.map((item)=>{
          if(value===item.sem){
            setSemerror("Cannot Enter for sem already entered")
          }
        })
        return {
          ...prev,
          sem: value,
        };
      } else if (name === 'percentage') {
        if(value>10){
          setCgpaerror("Value cannot be more than 10")
        }
        return {
          
          ...prev,
          percentage: value,
        };
      }
    });
  };

  const handleClick = () => {
    dispatch({
      type: 'SET_MODAL',
      modal: false,
    });
  };
  async function addsemcgpa(placement) {
    if(!semerror){
    try {
      let response = await http('post', 'semcgpa/add', { ...placement }, true);
      handleClick();
    } catch (err) {
      console.log(err);
    }
  }
  }

  return (
    <div className='h-screen w-screen bg-backdrop top-0 left-0 z-40 fixed flex items-center justify-center'>
      {/* Education Modal */}
      {props.type === 'education' && (
        <div className=' w-1/3 bg-white relative p-8'>
          <FiX
            onClick={() => handleClick()}
            className='font-semibold absolute top-4 text-xl cursor-pointer right-4'
          />
          <h1 className='font-bold text-lg'>SGPA Details</h1>
          <p className='text-dark text-sm'>
            Please add all information carefully
          </p>
          <div className='flex gap-3'>
            <ModalInput
              name='sem'
              value={marks.sem}
              onChange={handleMarksChange}
              title='Semester'
              type='number'
            />
            <ModalInput
              name='percentage'
              value={marks.percentage}
              onChange={handleMarksChange}
              title='CGPA'
              type='number'
            />
          </div>
          <span className="text-red-500 text-xs">{semerror}</span>
          <span className="text-red-500 text-xs">{cgpaerror}</span>
          <div className='text-center'>
            <button
              onClick={() => {
                handleClick();
                addsemcgpa(marks);
              }}
              className='bg-primary text-sm font-semibold focus:outline-none delay-75 ease-linear duration-100 transform hover:-translate-y-1 outline-none text-white rounded-lg px-10 py-2 mt-8'
            >
              Submit
            </button>
          </div>
        </div>
      )}

      {/* Hackathons Modal */}
      {props.type === 'hackathons' && <HackathonModal />}

      {/* Coding Competitions Modal */}
      {props.type === 'coding' && <CodingContestModal />}

      {/* Placement Modal */}
      {props.type === 'placement' && <PlacementModal />}

      {/* projects modal */}
      {props.type === 'projects' && <ProjectsModal />}

      {/* certifications modal */}
      {props.type === 'certifications' && <CertificationsModal />}

      {/* Internships Modal */}
      {props.type === 'internships' && <InternshipModal />}

      {/* skills modal */}
      {props.type === 'skills' && <SkillModal />}
    </div>
  );
};

export default Modal;
