import React, { useState, useEffect } from 'react';
import Card from '../../../components/Card';
import Header from '../../../components/Header';
import { http } from '../../../http';
import { useStateValue } from '../../../store/stateProvider';
import Modal from './Modals/Modal';

const Achivements = () => {
  const [modalType, setModalType] = useState('');
  const [{ modal, fetch }, dispatch] = useStateValue();
  const handleModal = (item) => {
    dispatch({
      type: 'SET_MODAL',
      modal: true,
    });
    setModalType(item);
  };
  async function getachievemts() {
    return http('get', 'achievements/get', {}, true);
  }
  const [achievements, setAchievements] = useState({
    project: [],
    hackathons: [],
    codingcontests: [],
  });
  // const [chng, setChng] = useState(false);
  // useEffect(async () => {
  //   dispatch({
  //     type: 'SET_MODAL',
  //     modal: false,
  //   });
  //   try {
  //     console.log(modal, 'to');
  //     let response = await getachievemts();
  //     console.log(response.data);
  //     setAchievements(response.data);
  //   } catch (err) {
  //     console.log(err);
  //   }
  // }, [chng]);
  const handleFetch = async () => {
    try {
      console.log(modal, 'to');
      let response = await getachievemts();
      console.log(response.data);
      setAchievements(response.data);
    } catch (err) {
      console.log(err);
    }
  };

  useEffect(() => {
    dispatch({
      type: 'SET_MODAL',
      modal: false,
    });
  }, []);

  useEffect(() => {
    setTimeout(() => {
      handleFetch();
    }, 1000);
  }, [modal]);

  return (
    <div>
      {modal && <Modal type={modalType} />}
      <Header headline='Achivements' type='student' />
      <div className='flex gap-8 px-5 mt-10'>
        <Card
          width='1/3'
          title='Projects'
          value={achievements.project.length}
        />
        <Card
          width='1/3'
          title='Hackathons'
          value={achievements.hackathons.length}
        />
        <Card
          width='1/3'
          title='Coding Contests'
          value={achievements.codingcontests.length}
        />
      </div>
      <div className='flex mx-5 gap-10 my-10'>
        <div className='w-full border-2 bg-white border-border rounded-lg'>
          <div className='py-5 px-6 flex justify-between'>
            <div>
              <h1 className='font-bold text-lg'>Hackathons</h1>
              <p className='text-dark text-sm'>
                Hackathons you have participated in
              </p>
            </div>
            <h1
              onClick={() => handleModal('hackathons')}
              className='text-primary cursor-pointer font-semibold'
            >
              Add Entry
            </h1>
          </div>
          {/* content */}
          <div className='w-full text-dark font-bold flex justify-between px-8 py-4 border-b border-border'>
            <p className='text-sm'>Name</p>
            <p className='text-sm'>Position</p>
          </div>
          {achievements.hackathons.map((item) => {
            return (
              <div
                className='w-full flex justify-between px-8 py-4 border-b border-border'
                key={item.position}
              >
                <p className='text-sm'>{item.name}</p>
                <p className='text-sm'>{item.position}</p>
              </div>
            );
          })}
        </div>
        <div className='w-full border-2 bg-white border-border rounded-lg'>
          <div className='py-5 px-6 flex justify-between'>
            <div>
              <h1 className='font-bold text-lg'>Coding Contests</h1>
              <p className='text-dark text-sm'>
                Coding contests you have participated in
              </p>
            </div>
            <h1
              onClick={() => handleModal('coding')}
              className='text-primary cursor-pointer font-semibold'
            >
              Add Entry
            </h1>
          </div>
          <div className='w-full flex justify-between text-dark font-bold px-8 py-4 border-b border-border'>
            <p className='text-sm'>Name</p>
            <p className='text-sm'>Position</p>
          </div>
          {achievements.codingcontests.map((item) => {
            return (
              <div
                className='w-full flex justify-between px-8 py-4 border-b border-border'
                key={item.position}
              >
                <p className='text-sm'>{item.name}</p>
                <p className='text-sm'>{item.position}</p>
              </div>
            );
          })}
        </div>
      </div>
      <div className='px-5 mb-10'>
        <div className='w-full border-2 bg-white border-border rounded-lg'>
          <div className='py-5 px-6 flex justify-between'>
            <div>
              <h1 className='font-bold text-lg'>Projects</h1>
              <p className='text-dark text-sm'>Your awesome work!</p>
            </div>
            <h1
              onClick={() => handleModal('projects')}
              className='text-primary cursor-pointer font-semibold'
            >
              Add Entry
            </h1>
          </div>
          <div className='w-full text-dark font-bold flex justify-between px-8 py-4 border-b border-border'>
            <p className='text-sm'>Name</p>
            <p className='text-sm'>Tech Stack</p>
            <p className='text-sm'>Description</p>
            <p className='text-sm'>Link</p>
          </div>
          {achievements.project.map((item) => {
            return (
              <div
                className='w-full flex justify-between px-8 py-4 border-b border-border'
                key={item.n}
              >
                <p className='text-sm'>{item.name}</p>
                <p className='text-sm'>{item.techstack}</p>
                <p className='text-sm'>{item.description}</p>
                <p className='text-sm'>{item.link}</p>
              </div>
            );
          })}
        </div>
      </div>
    </div>
  );
};

export default Achivements;
